import { FunctionComponent, useCallback } from "react";
import { CircularProgress, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { useParams } from "react-router";
import { gql, useQuery, useMutation } from "@apollo/client";

import { Question } from "../../types";
import QuizEditor from "./QuizEditor";
import ExerciseEditor from "./ExerciseEditor";
import LessonEditor from "./LessonEditor";

const useStyles = makeStyles((theme) => ({
    secondaryRoot: {
        width: "100%",
        height: "calc(100vh - 136px)",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
    },
    errorMessage: {
        textAlign: "center",
        color: "white",
        fontSize: 20,
        maxWidth: 600,
    },
}));

interface Params {
    courseId: string;
    chapterId: string;
    sectionId: string;
}

const GET_SECTION = gql`
    query GetSection($sectionId: ID!) {
        getSectionById(sectionId: $sectionId) {
            id
            title
            type
            description
            content
            questions {
                text
                type
                options {
                    text
                    correct
                }
            }
            exercise {
                id
                title
                description
                handle
                tags
                tests {
                    id
                    title
                    description
                }
            }
        }
    }
`;

const UPDATE_SECTION = gql`
    mutation UpdateSection(
        $sectionId: ID!
        $title: String
        $description: String
        $content: String
        $questions: [QuestionInput!]
        $exercise: ID
    ) {
        updateSection(
            sectionId: $sectionId
            title: $title
            description: $description
            content: $content
            questions: $questions
            exercise: $exercise
        ) {
            id
        }
    }
`;

const ContentEditor: FunctionComponent = () => {
    const classes = useStyles();
    const params = useParams<Params>();
    const {
        loading: fetching,
        error: fetchError,
        data: currentSectionData,
    } = useQuery(GET_SECTION, { variables: { sectionId: params.sectionId } });
    const [updateSection, { loading: updating, error: updateError }] =
        useMutation(UPDATE_SECTION, {
            refetchQueries: [GET_SECTION],
        });

    const currentSection = currentSectionData?.getSectionById || {};

    const handleSaveLesson = useCallback(
        (title: string, description: string, content: string) => {
            updateSection({
                variables: {
                    sectionId: currentSection?.id,
                    title,
                    description,
                    content,
                },
            });
        },
        [currentSection?.id, updateSection],
    );

    const handleSaveExercise = useCallback(
        (title: string, description: string, exercise?: string) => {
            console.log(exercise);
            updateSection({
                variables: {
                    sectionId: currentSection?.id,
                    title,
                    description,
                    exercise,
                },
            });
        },
        [currentSection?.id, updateSection],
    );

    const handleSaveQuiz = useCallback(
        (title: string, description: string, questions: Question[]) => {
            updateSection({
                variables: {
                    sectionId: currentSection?.id,
                    title,
                    description,
                    questions: questions.map((question) => ({
                        text: question.text,
                        type: question.type,
                        options: question.options.map((option) => ({
                            text: option.text,
                            correct: option.correct,
                        })),
                    })),
                },
            });
        },
        [currentSection?.id, updateSection],
    );

    if (currentSection.type === "quiz") {
        return (
            <QuizEditor
                title={currentSection?.title}
                description={currentSection?.description}
                questions={currentSection?.questions}
                updating={updating}
                onSave={handleSaveQuiz}
            />
        );
    }

    if (currentSection.type === "coding_excercise") {
        return (
            <ExerciseEditor
                title={currentSection?.title}
                description={currentSection?.description}
                exercise={currentSection?.exercise}
                updating={updating}
                onSave={handleSaveExercise}
            />
        );
    }

    if (currentSection.type === "article") {
        return (
            <LessonEditor
                title={currentSection?.title}
                description={currentSection?.description}
                content={currentSection?.content}
                updating={updating}
                onSave={handleSaveLesson}
            />
        );
    }

    if (fetching) {
        return (
            <div className={classes.secondaryRoot}>
                <CircularProgress />
            </div>
        );
    }

    return (
        <div className={classes.secondaryRoot}>
            <Typography className={classes.errorMessage}>
                Error: Invalid section type "
                {currentSection?.type ?? "invalid_state"}". Please contact the
                administrator for further assitance.
            </Typography>
        </div>
    );
};

export default ContentEditor;
