import {
    Fragment,
    FunctionComponent,
    useState,
    useEffect,
    useCallback,
} from "react";
import {
    Chip,
    Typography,
    Button,
    CircularProgress,
    Icon,
} from "@material-ui/core";
import { Autocomplete, createFilterOptions } from "@material-ui/lab";
import { makeStyles } from "@material-ui/core/styles";
import Formsy from "formsy-react";
import { gql, useLazyQuery } from "@apollo/client";

import TextField from "../common/FormsyTextField";

const useStyles = makeStyles((theme) => ({
    root: {
        display: "flex",
        flexDirection: "column",
        justifyExercise: "flex-start",
        width: "100%",
        maxWidth: 800,
    },
    title: {},
    description: {
        marginTop: theme.spacing(2),
    },

    exercise: {
        width: "100%",
        marginTop: theme.spacing(2),
    },
    exerciseOption: {
        flexDirection: "row",
        display: "flex",
        paddingTop: 4,
        paddingBottom: 4,
    },
    exerciseTexts: {
        display: "flex",
        flexDirection: "column",
        marginLeft: 16,
    },
    exerciseOptionHandle: {
        fontSize: 14,
        fontWeight: "bold",
    },
    exerciseOptionTitle: {
        fontSize: 12,
    },
    exerciseTitle: {
        marginTop: theme.spacing(4),
        fontWeight: "bold",
        fontSize: 16,
    },
    exerciseDescription: {
        marginTop: theme.spacing(2),
    },
    testListItem: {
        marginTop: theme.spacing(1),
    },
    exerciseTag: {
        marginLeft: theme.spacing(1),
    },
    actions: {
        marginTop: theme.spacing(4),
        width: "100%",
        display: "flex",
        justifyExercise: "flex-end",
        flexDirection: "row",
    },
    action: {
        width: 128,
    },
    actionIcon: {
        marginRight: theme.spacing(1),
    },
    progress: {
        marginRight: theme.spacing(1),
    },
}));

interface Props {
    title: string;
    description: string;
    exercise: string | undefined;
    updating: boolean;
    onSave: (title: string, description: string, exercise?: string) => void;
}

const GET_TEST_SUITES = gql`
    query GetTestSuites($search: String) {
        getTestSuites(search: $search) {
            records {
                id
                handle
                title
                description
                tags
                tests {
                    id
                    title
                    description
                }
            }
        }
    }
`;

const LessonEditor: FunctionComponent<Props> = (props: Props) => {
    const { onSave, updating } = props;
    const classes = useStyles();
    const [valid, setValid] = useState(false);
    const [title, setTitle] = useState("");
    const [exercise, setExercise] = useState<any | null>(null);
    const [description, setDescription] = useState("");
    const [search, setSearch] = useState("");

    const [exerciseOptionsOpen, setExerciseOptionsOpen] = useState(false);

    const [
        fetchTestSuites,
        { loading: fetching, error: fetchError, data, previousData },
    ] = useLazyQuery(GET_TEST_SUITES);

    console.log(data?.getTestSuites);

    const changesMade =
        props?.title !== title ||
        props?.exercise !== exercise ||
        props?.description !== description;

    useEffect(() => {
        setTitle(props.title || "");
        setDescription(props.description || "");
        setExercise(props.exercise || null);
    }, [props.exercise, props.description, props.title]);

    const handleTitleChange = useCallback((event) => {
        setTitle(event.target.value);
    }, []);

    const handleDescriptionChange = useCallback((event) => {
        setDescription(event.target.value);
    }, []);

    const handleValidForm = useCallback(() => {
        setValid(true);
    }, []);

    const handleInvalidForm = useCallback(() => {
        setValid(false);
    }, []);

    const handleSave = useCallback(() => {
        onSave(title, description, exercise?.id ?? null);
    }, [exercise?.id, onSave, title, description]);

    const handleSearchChange = useCallback(
        (event: any) => {
            setSearch(event.target.value);
            fetchTestSuites({
                variables: { search: event.target.value },
            });
        },
        [fetchTestSuites],
    );

    const handleExerciseChange = (_event: any, newExercise: any) => {
        setExercise(newExercise);
    };

    const exerciseFilterOptions = createFilterOptions({
        matchFrom: "any",
        ignoreAccents: true,
        ignoreCase: true,
        stringify: (option: any) => option.handle,
    });

    const handleExerciseOptionsToggle = useCallback(() => {
        setExerciseOptionsOpen((exerciseOptionsOpen) => !exerciseOptionsOpen);
    }, []);

    const renderOption = useCallback(
        (option: any) => (
            <div className={classes.exerciseOption}>
                <div className={classes.exerciseTexts}>
                    <span className={classes.exerciseOptionHandle}>
                        {option.handle}
                    </span>
                    <span className={classes.exerciseOptionTitle}>
                        {option.title}
                    </span>
                </div>
            </div>
        ),
        [
            classes.exerciseOption,
            classes.exerciseOptionHandle,
            classes.exerciseOptionTitle,
            classes.exerciseTexts,
        ],
    );

    const renderInput = useCallback(
        (params0) => (
            <TextField
                name="exercise"
                {...params0}
                label="Exercise"
                variant="outlined"
                InputProps={{
                    ...params0.InputProps,
                    endAdornment: (
                        <Fragment>
                            {fetching ? (
                                <CircularProgress color="inherit" size={20} />
                            ) : null}
                            {params0.InputProps.endAdornment}
                        </Fragment>
                    ),
                }}
                value={search}
                onChange={handleSearchChange}
            />
        ),
        [fetching, handleSearchChange, search],
    );

    const getOptionsSelected = useCallback(
        (option: any, value: any) => option.id === value.id,
        [],
    );

    const getOptionLabel = useCallback(
        (option: any) => option.handle || "<unavailable>",
        [],
    );

    const renderExerciseSelect = () => (
        <Autocomplete
            filterOptions={exerciseFilterOptions}
            open={exerciseOptionsOpen}
            onOpen={handleExerciseOptionsToggle}
            onClose={handleExerciseOptionsToggle}
            renderOption={renderOption}
            className={classes.exercise}
            options={
                data?.getTestSuites?.records ??
                previousData?.getTestSuites?.records ??
                []
            }
            loading={!previousData?.getTestSuites?.records && fetching}
            renderInput={renderInput}
            getOptionSelected={getOptionsSelected}
            getOptionLabel={getOptionLabel}
            value={exercise}
            onChange={handleExerciseChange}
        />
    );

    const renderTestSuite = () => (
        <div style={{ color: "white" }}>
            <Typography className={classes.exerciseTitle}>
                {exercise?.title ?? "<invalid_title>"}
            </Typography>

            <Typography className={classes.exerciseDescription}>
                {exercise?.description ?? "<invalid_description>"}
            </Typography>

            <ul>
                {exercise?.tests.map((test: any) => (
                    <li className={classes.testListItem}>
                        <div>
                            <Typography>{test.title}</Typography>
                            <Typography>{test.description}</Typography>
                        </div>
                    </li>
                ))}
            </ul>

            {exercise?.tags.map((tag: string) => (
                <Chip label={tag} className={classes.exerciseTag} />
            ))}
        </div>
    );

    return (
        <div className={classes.root}>
            <Formsy onValid={handleValidForm} onInvalid={handleInvalidForm}>
                <TextField
                    name="title"
                    className={classes.title}
                    label="Title"
                    validations={{
                        minLength: 8,
                        maxLength: 504,
                    }}
                    validationErrors={{
                        minLength: "The title should have 8-504 characters",
                        maxLength: "The title should have 8-504 characters",
                    }}
                    variant="outlined"
                    size="medium"
                    value={title}
                    onChange={handleTitleChange}
                    required={true}
                    fullWidth={true}
                />

                <TextField
                    name="description"
                    className={classes.description}
                    label="Description"
                    validations={{
                        maxLength: 1024,
                    }}
                    validationErrors={{
                        maxLength:
                            "The description cannot be longer than 1024 characters",
                    }}
                    variant="outlined"
                    size="medium"
                    value={description}
                    onChange={handleDescriptionChange}
                    fullWidth={true}
                    multiline={true}
                    rows={5}
                />
                {renderExerciseSelect()}
                {exercise && renderTestSuite()}
            </Formsy>
            <div className={classes.actions}>
                <Button
                    variant="contained"
                    className={classes.action}
                    disabled={!changesMade || !valid || updating}
                    onClick={handleSave}>
                    {updating && (
                        <CircularProgress
                            size="16px"
                            color="inherit"
                            className={classes.progress}
                        />
                    )}
                    {!updating && (
                        <Icon className={classes.actionIcon}>save</Icon>
                    )}
                    Save
                </Button>
            </div>
        </div>
    );
};

export default LessonEditor;
