import {
    FunctionComponent,
    useState,
    useCallback,
    useRef,
    useEffect,
} from "react";
import { Typography, Icon, IconButton, ButtonBase } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { DragDropContext, Droppable, DropResult } from "react-beautiful-dnd";
import { useConfirm } from "material-ui-confirm";

import CourseSection from "./CourseSection";
import SectionEditor from "./SectionEditor";
import { Section } from "../../types";
import { reorder } from "../../util";
import { useHover } from "../../hooks";

const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 736,
        background: (theme.palette.background as any).level2,
        padding: theme.spacing(3),
        marginBottom: theme.spacing(2),
        borderRadius: theme.shape.borderRadius,
    },
    sections: {
        width: "100%",
        borderRadius: theme.shape.borderRadius,
    },
    chapter: {
        background: theme.palette.background.paper,
        userSelect: "none",
        padding: theme.spacing(2),
        marginTop: theme.spacing(2),
    },
    chapterHeader: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-start",
        alignItems: "center",
        marginBottom: theme.spacing(4),
    },
    chapterNumberIcon: {
        color: "white",
        marginRight: theme.spacing(1),
    },
    chapterNumber: { color: "white", marginRight: theme.spacing(2) },
    chapterTitle: { color: "white" },
    secondaryActions: {
        display: "flex",
        justifyContent: "center",
        width: "100%",
    },
    newSection: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2),
        paddingTop: theme.spacing(1),
        paddingBottom: theme.spacing(1),
        marginTop: theme.spacing(2),
        alignSelf: "center",
        borderRadius: theme.shape.borderRadius,
    },
    newSectionIcon: {
        color: "white",
        marginRight: theme.spacing(1),
        fontSize: 18,
    },
    newSectionTitle: { color: "white", fontSize: 14 },
    header: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        width: "100%",
    },
    details: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-start",
    },
    actions: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-end",
        alignItems: "center",
        marginLeft: "auto",
    },
    dragIndicator: {
        width: 20,
        height: 20,
        color: "white",
    },
}));

export interface Props {
    id?: string;
    index: number;
    title?: string;
    sections?: Section[];

    onReorderSections: (chapterId: string, sections: Section[]) => void;
    onEditChapter: (chapterId: string) => void;
    onDeleteChapter: (chapterId: string) => void;

    onSaveSection: (section: Section, parentChapterId: string) => void;
    onDeleteSection: (chapterId: string, sectionId: string) => void;
    onEdit: (chapterId: string, sectionId: string) => void;
}

const CourseChapter: FunctionComponent<Props> = (props) => {
    const {
        id,
        index,
        title,
        sections,

        onReorderSections,
        onEditChapter,
        onDeleteChapter,

        onSaveSection,
        onDeleteSection,
        onEdit,
    } = props;
    const classes = useStyles();
    const { hover, onMouseEnter, onMouseLeave } = useHover();
    const [openNewSection, setOpenNewSection] = useState(false);
    const confirm = useConfirm();

    // TODO: Update this!
    const creating = false;
    const previousCreating = useRef(false);

    useEffect(() => {
        if (previousCreating.current && !creating) {
            setOpenNewSection(false);
        }

        previousCreating.current = creating;
    }, [creating]);

    const handleDragEnd = useCallback(
        (result: DropResult) => {
            if (!result.destination) {
                return;
            }

            const newSections = reorder(
                sections || [],
                result.source.index,
                result.destination.index,
            );
            console.log("before", sections);
            console.log("after", newSections);
            onReorderSections(id, newSections);
        },
        [id, onReorderSections, sections],
    );

    const handleEditChapter = useCallback(() => {
        onEditChapter(id);
    }, [id, onEditChapter]);

    const handleDeleteChapter = useCallback(() => {
        confirm({
            title: "Are you sure you want to delete the chapter?",
            description: `You cannot restore it once you delete. This action will permanently delete "${title}", including its sections.`,
            confirmationText: "Yes, I understand. Delete anyway.",
            confirmationButtonProps: {
                color: "secondary",
                variant: "contained",
            },
        }).then(() => {
            onDeleteChapter(id);
        });
    }, [confirm, id, onDeleteChapter, title]);

    const handleNewSection = useCallback(() => {
        setOpenNewSection(true);
    }, [setOpenNewSection]);

    const handleCancelNewSection = useCallback(() => {
        setOpenNewSection(false);
    }, [setOpenNewSection]);

    const handleSaveNewSection = useCallback(
        (section) => {
            onSaveSection(section, id);
        },
        [id, onSaveSection],
    );

    return (
        <>
            <div className={classes.root}>
                <div
                    className={classes.chapterHeader}
                    onMouseEnter={onMouseEnter}
                    onMouseLeave={onMouseLeave}>
                    <div className={classes.header}>
                        <div className={classes.details}>
                            <Icon
                                fontSize="small"
                                className={classes.chapterNumberIcon}>
                                bookmark
                            </Icon>
                            <Typography className={classes.chapterNumber}>
                                Chapter {index}
                            </Typography>
                            <Icon
                                fontSize="small"
                                className={classes.chapterNumberIcon}>
                                topic
                            </Icon>
                            <Typography className={classes.chapterTitle}>
                                {title}
                            </Typography>
                        </div>
                        <div
                            className={classes.actions}
                            style={{
                                visibility: hover ? "visible" : "hidden",
                            }}>
                            <IconButton
                                size="small"
                                onClick={handleEditChapter}>
                                <Icon fontSize="small">edit</Icon>
                            </IconButton>
                            <IconButton
                                size="small"
                                onClick={handleDeleteChapter}>
                                <Icon fontSize="small">delete</Icon>
                            </IconButton>
                            <Icon
                                fontSize="small"
                                className={classes.dragIndicator}>
                                drag_indicator
                            </Icon>
                        </div>
                    </div>
                </div>
                <DragDropContext onDragEnd={handleDragEnd}>
                    <Droppable droppableId="droppable">
                        {(provided, snapshot) => (
                            <div
                                {...provided.droppableProps}
                                ref={provided.innerRef}
                                className={classes.sections}>
                                {sections?.map((section, index) => (
                                    <CourseSection
                                        key={section.id}
                                        index={index}
                                        sectionId={section.id}
                                        chapterId={id}
                                        title={section.title}
                                        type={section.type}
                                        onDelete={onDeleteSection}
                                        onEdit={onEdit}
                                    />
                                ))}
                                {provided.placeholder}
                            </div>
                        )}
                    </Droppable>
                </DragDropContext>
                {openNewSection && (
                    <SectionEditor
                        onCancel={handleCancelNewSection}
                        onSave={handleSaveNewSection}
                        creating={openNewSection && creating}
                    />
                )}
                {!openNewSection && (
                    <div className={classes.secondaryActions}>
                        <ButtonBase
                            onClick={handleNewSection}
                            className={classes.newSection}>
                            <Icon className={classes.newSectionIcon}>
                                add_circle
                            </Icon>
                            <Typography className={classes.newSectionTitle}>
                                New Section
                            </Typography>
                        </ButtonBase>
                    </div>
                )}
            </div>
        </>
    );
};

export default CourseChapter;
