import { FunctionComponent, useState, useEffect, useCallback } from "react";
import { Button, CircularProgress, Icon } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Formsy from "formsy-react";

import TextField from "../common/FormsyTextField";

const useStyles = makeStyles((theme) => ({
    root: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "flex-start",
        width: "100%",
        maxWidth: 800,
    },
    title: {},
    description: {
        marginTop: theme.spacing(2),
    },
    content: {
        marginTop: theme.spacing(2),
    },
    contentTextArea: {
        resize: "vertical",
        minHeight: 400,
        width: "100%",
    },
    actions: {
        marginTop: theme.spacing(2),
        width: "100%",
        display: "flex",
        justifyContent: "flex-end",
        flexDirection: "row",
    },
    action: {
        width: 128,
    },
    actionIcon: {
        marginRight: theme.spacing(1),
    },
    progress: {
        marginRight: theme.spacing(1),
    },
}));

interface Props {
    title: string;
    description: string;
    content: string;
    updating: boolean;
    onSave: (title: string, description: string, content: string) => void;
}

const LessonEditor: FunctionComponent<Props> = (props: Props) => {
    const { onSave, updating } = props;
    const classes = useStyles();
    const [valid, setValid] = useState(false);
    const [title, setTitle] = useState("");
    const [content, setContent] = useState("");
    const [description, setDescription] = useState("");

    const changesMade =
        props?.title !== title ||
        props?.content !== content ||
        props?.description !== description;

    useEffect(() => {
        setTitle(props.title || "");
        setDescription(props.description || "");
        setContent(props.content || "");
    }, [props.content, props.description, props.title]);

    const handleTitleChange = useCallback((event) => {
        setTitle(event.target.value);
    }, []);

    const handleDescriptionChange = useCallback((event) => {
        setDescription(event.target.value);
    }, []);

    const handleContentChange = useCallback((event) => {
        setContent(event.target.value);
    }, []);

    const handleValidForm = useCallback(() => {
        setValid(true);
    }, []);

    const handleInvalidForm = useCallback(() => {
        setValid(false);
    }, []);

    const handleSave = useCallback(() => {
        onSave(title, description, content);
    }, [description, onSave, content, title]);

    return (
        <div className={classes.root}>
            <Formsy onValid={handleValidForm} onInvalid={handleInvalidForm}>
                <TextField
                    name="title"
                    className={classes.title}
                    label="Title"
                    validations={{
                        minLength: 8,
                        maxLength: 504,
                    }}
                    validationErrors={{
                        minLength: "The title should have 8-504 characters",
                        maxLength: "The title should have 8-504 characters",
                    }}
                    variant="outlined"
                    size="medium"
                    value={title}
                    onChange={handleTitleChange}
                    required={true}
                    fullWidth={true}
                />

                <TextField
                    name="description"
                    className={classes.description}
                    label="Description"
                    validations={{
                        maxLength: 1024,
                    }}
                    validationErrors={{
                        maxLength:
                            "The description cannot be longer than 1024 characters",
                    }}
                    variant="outlined"
                    size="medium"
                    value={description}
                    onChange={handleDescriptionChange}
                    fullWidth={true}
                    multiline={true}
                    rows={5}
                />
                <TextField
                    name="content"
                    className={classes.content}
                    label="Content"
                    validations={{
                        maxLength: 10240,
                    }}
                    validationErrors={{
                        maxLength:
                            "The article content cannot be longer than 10240 characters",
                    }}
                    variant="outlined"
                    size="medium"
                    value={content}
                    onChange={handleContentChange}
                    fullWidth={true}
                    multiline={true}
                    inputProps={{
                        className: classes.contentTextArea,
                    }}
                />
            </Formsy>
            <div className={classes.actions}>
                <Button
                    variant="contained"
                    className={classes.action}
                    disabled={!changesMade || !valid || updating}
                    onClick={handleSave}>
                    {updating && (
                        <CircularProgress
                            size="16px"
                            color="inherit"
                            className={classes.progress}
                        />
                    )}
                    {!updating && (
                        <Icon className={classes.actionIcon}>save</Icon>
                    )}
                    Save
                </Button>
            </div>
        </div>
    );
};

export default LessonEditor;
