import { FunctionComponent, useState, useCallback } from "react";
import { makeStyles } from "@material-ui/core/styles";

import { useAppSelector } from "../../redux/hooks";
import NoRecords from "../common/NoRecords";
import CouponEditor from "./CouponEditor";

const useStyles = makeStyles(() => ({
    noRecords: {
        width: "100%",
        display: "flex",
        flexDirection: "column",
        justifyContent: "flex-start",
        alignItems: "center",
        height: "calc(100vh - 128px)"
    },
}));

const CourseDetails: FunctionComponent = () => {
    const classes = useStyles();
    const { firstName } = useAppSelector((state) => state.session.payload);
    const [openCouponEditor, setOpenCouponEditor] = useState(false);

    const handleCloseCouponEditor = useCallback(() => {
        setOpenCouponEditor(false);
    }, []);

    const handleSaveCoupon = useCallback(() => {
        setOpenCouponEditor(false);
    }, []);

    const handleNewCoupon = useCallback(() => {
        setOpenCouponEditor(true);
    }, [setOpenCouponEditor]);

    return (
        <>
            <div className={classes.noRecords}>
                <NoRecords
                    message={`Are you ready to create your first coupon, ${firstName}?`}
                    actionText="Create New Coupon"
                    actionHandler={handleNewCoupon}
                    image={`${process.env.PUBLIC_URL}/assets/images/empty-articles.svg`}
                    action={true}
                />
            </div>
            <CouponEditor
                open={openCouponEditor}
                onClose={handleCloseCouponEditor}
                onSave={handleSaveCoupon}
            />
        </>
    );
};

export default CourseDetails;
