import { FunctionComponent, useCallback } from "react";
import { Radio, Checkbox, Icon, IconButton } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

import TextField from "../common/FormsyTextField";
import { useHover } from "../../hooks";
import { QuestionType } from "../../types";

const useStyles = makeStyles((theme) => ({
    option: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-start",
        marginTop: theme.spacing(2),
    },
    left: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-start",
        marginRight: theme.spacing(2),
    },
    right: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-end",
        alignItems: "center",
    },
    optionCheckbox: {},
    optionText: {
        marginLeft: theme.spacing(1),
        width: 340,
    },
    actionIcon: {
        fontSize: 18,
    },
}));

interface Props {
    index: number;
    text: string;
    correct: boolean;
    type: QuestionType;
    onDelete?: (index: number) => void;
    onNew: (index: number) => void;
    onTextChange: (index: number, text: string) => void;
    onCorrectChange: (index: number, correct: boolean) => void;
}

const QuestionOption: FunctionComponent<Props> = (props: Props) => {
    const {
        text,
        correct,
        index,
        type,
        onNew,
        onDelete,
        onTextChange,
        onCorrectChange,
    } = props;
    const { hover, onMouseEnter, onMouseLeave } = useHover();
    const classes = useStyles();

    const handleDelete = useCallback(() => {
        onDelete && onDelete(index);
    }, [index, onDelete]);

    const handleNew = useCallback(() => {
        onNew(index);
    }, [index, onNew]);

    const handleTextChange = useCallback(
        (event) => {
            onTextChange(index, event.target.value);
        },
        [index, onTextChange],
    );

    const handleCorrectChange = useCallback(
        (event) => {
            onCorrectChange(index, event.target.checked);
        },
        [index, onCorrectChange],
    );

    const renderActions = () => (
        <div className={classes.right}>
            {onDelete && (
                <IconButton onClick={handleDelete}>
                    <Icon className={classes.actionIcon}>delete</Icon>
                </IconButton>
            )}
            <IconButton onClick={handleNew}>
                <Icon className={classes.actionIcon}>add_circle</Icon>
            </IconButton>
        </div>
    );

    const Checked = type === "single_correct_option" ? Radio : Checkbox;

    return (
        <div
            className={classes.option}
            onMouseEnter={onMouseEnter}
            onMouseLeave={onMouseLeave}>
            <div className={classes.left}>
                <Checked
                    onChange={handleCorrectChange}
                    checked={correct}
                    className={classes.optionCheckbox}
                />
                <TextField
                    name={`option-${index}`}
                    className={classes.optionText}
                    validations={{
                        minLength: 1,
                        maxLength: 504,
                    }}
                    validationErrors={{
                        minLength: "The option should have 1-128 characters",
                        maxLength: "The option should have 1-128 characters",
                    }}
                    size="medium"
                    value={text}
                    fullWidth={true}
                    placeholder={`Option ${index + 1}`}
                    onChange={handleTextChange}
                />
            </div>
            {hover && renderActions()}
        </div>
    );
};

export default QuestionOption;
