import {
    FunctionComponent,
    useRef,
    useEffect,
    useState,
    useCallback,
} from "react";
import {
    Drawer,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    Button,
    Typography,
    Toolbar,
    AppBar,
    CircularProgress,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Formsy from "formsy-react";

import { Section } from "../../types";
import TextField from "../common/FormsyTextField";

const types = [
    {
        title: "Video",
        value: "video",
    },
    {
        title: "Article",
        value: "article",
    },
    {
        title: "Quiz",
        value: "quiz",
    },
    {
        title: "Coding Excercise",
        value: "coding_excercise",
    },
    {
        title: "Assignment",
        value: "assignment",
    },
];

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-between",
        marginTop: theme.spacing(4),
    },
    top: {},
    toolbar: {},
    toolbarContent: {
        width: "100%",
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        alignItems: "center",
    },
    actions: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-end",
        alignItems: "center",
    },
    form: {
        display: "flex",
        flexDirection: "column",
    },
    title: {
        fontWeight: "bold",
        fontSize: 20,
        maxWidth: 800,
    },
    type: {
        marginTop: theme.spacing(2),
        width: 320,
    },
    description: {
        marginTop: theme.spacing(2),
        maxWidth: 800,
    },
    action: {
        width: 96,
        marginLeft: theme.spacing(1),
    },
    actionIcon: {
        marginRight: theme.spacing(1),
    },
    progress: {
        marginRight: theme.spacing(1),
    },
}));

export interface Props {
    creating: boolean;
    onCancel: () => void;
    onSave: (section: Section) => void;
}

const SectionEditor: FunctionComponent<Props> = (props: Props) => {
    const { onCancel, onSave, creating } = props;
    const classes = useStyles();
    const [title, setTitle] = useState("");
    const [type, setType] = useState("article");
    const [valid, setValid] = useState(false);

    const handleTitleChange = useCallback((event) => {
        setTitle(event.target.value);
    }, []);

    const handleTypeChange = useCallback((event) => {
        setType(event.target.value);
    }, []);

    const handleValidForm = useCallback(() => {
        setValid(true);
    }, []);

    const handleInvalidForm = useCallback(() => {
        setValid(false);
    }, []);

    const handleSave = useCallback(() => {
        onSave({
            title,
            type,
        });
    }, [onSave, title, type]);

    return (
        <div className={classes.root}>
            <div className={classes.top}>
                <Formsy
                    className={classes.form}
                    onValid={handleValidForm}
                    onInvalid={handleInvalidForm}>
                    <TextField
                        name="title"
                        className={classes.title}
                        label="Title"
                        validations={{
                            minLength: 8,
                            maxLength: 504,
                        }}
                        validationErrors={{
                            minLength: "The title should have 8-504 characters",
                            maxLength: "The title should have 8-504 characters",
                        }}
                        variant="outlined"
                        size="medium"
                        value={title}
                        onChange={handleTitleChange}
                        required={true}
                        fullWidth={true}
                    />
                    <FormControl
                        variant="outlined"
                        className={classes.type}
                        size="medium"
                        required={true}>
                        <InputLabel id="typeLabel">Type</InputLabel>
                        <Select
                            labelId="typeLabel"
                            value={type}
                            onChange={handleTypeChange}
                            label="Type">
                            {types.map((type) => (
                                <MenuItem key={type.value} value={type.value}>
                                    {type.title}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                </Formsy>
            </div>
            <div className={classes.actions}>
                <Button
                    variant="outlined"
                    className={classes.action}
                    onClick={onCancel}>
                    Cancel
                </Button>
                <Button
                    variant="contained"
                    className={classes.action}
                    disabled={!valid || creating}
                    onClick={handleSave}>
                    {creating && (
                        <CircularProgress
                            size="16px"
                            color="inherit"
                            className={classes.progress}
                        />
                    )}
                    Save
                </Button>
            </div>
        </div>
    );
};

export default SectionEditor;
