import { FunctionComponent, useCallback, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Icon, Typography, ButtonBase } from "@material-ui/core";
import { useHistory } from "react-router-dom";

import CourseChapter from "./CourseChapter";
import ChapterEditor from "./ChapterEditor";
import SectionEditor from "./SectionEditor";
import { Chapter, Section } from "../../types";
import { useAppSelector } from "../../redux/hooks";

import NoRecords from "../common/NoRecords";

const useStyles = makeStyles((theme) => ({
    root: {
        background: "transparent",
        width: "100%"
    },
    noRecords: {
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "flex-start",
        height: "calc(100vh - 128px)",
        width: "100%",
    },
    secondaryActions: {
        display: "flex",
        justifyContent: "center",
        width: 736,
    },
    newChapter: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2),
        paddingTop: theme.spacing(1),
        paddingBottom: theme.spacing(1),
        marginTop: theme.spacing(2),
        alignSelf: "center",
        borderRadius: theme.shape.borderRadius,
    },
    newChapterIcon: {
        color: "white",
        marginRight: theme.spacing(1),
        fontSize: 18,
    },
    newChapterTitle: { color: "white", fontSize: 14 },
}));

interface Props {
    courseId: string;
    chapters: Chapter[];
    onUpdateChapter: (chapter: any) => void;
    onCreateChapter: (chapter: any) => void;
    onDeleteChapter: (chapterId: string) => void;

    onCreateSection: (section: any) => void;
    onDeleteSection: (chapterId: string, sectionId: string) => void;
}

const CourseCurriculum: FunctionComponent<Props> = (props: Props) => {
    const {
        courseId,
        chapters,
        onUpdateChapter,
        onCreateChapter,
        onDeleteChapter,

        onCreateSection,
        onDeleteSection,
    } = props;
    const classes = useStyles();
    const [openChapterEditor, setOpenChapterEditor] = useState(false);
    const [currentChapter, setCurrentChapter] = useState<Chapter | null>(null);
    const history = useHistory();
    const { firstName } = useAppSelector((state) => state.session.payload);

    const handleNewChapter = useCallback(() => {
        setCurrentChapter(null);
        setOpenChapterEditor(true);
    }, []);

    const handleCloseChapterEditor = useCallback(() => {
        setOpenChapterEditor(false);
    }, []);

    /* onUpdateChapter discards courseId. So it's alright to pass it. */
    const handleSaveChapter = useCallback(
        (chapter: Chapter) => {
            (chapter.chapterId ? onUpdateChapter : onCreateChapter)({
                ...chapter,
                courseId,
            });
        },
        [courseId, onCreateChapter, onUpdateChapter],
    );

    const handleReorderSections = useCallback(
        (chapterId: string, sections: Section[]) => {
            onUpdateChapter({
                chapterId,
                sections: sections.map((section): string => section.id),
            });
        },
        [onUpdateChapter],
    );

    const handleEditChapter = useCallback(
        (chapterId: string) => {
            const newCurrentChapter =
                chapters.find((chapter) => chapter.id === chapterId) || null;
            setCurrentChapter(newCurrentChapter);
            setOpenChapterEditor(true);
        },
        [chapters],
    );

    const handleDeleteChapter = useCallback((chapterId: string) => {
        onDeleteChapter(chapterId);
    }, [onDeleteChapter]);

    /* Callbacks passed to `SectionEditor` */
    const handleSaveSection = useCallback(
        (section: Section, parentChapterId) => {
            onCreateSection({
                ...section,
                chapterId: parentChapterId,
            });
        },
        [onCreateSection],
    );

    const handleDeleteSection = useCallback(
        (chapterId: string, sectionId: string) => {
            onDeleteSection(chapterId, sectionId);
        },
        [onDeleteSection],
    );

    const handleAddContent = useCallback(
        (chapterId: string, sectionId: string) => {
            const chapter = chapters.find(
                (chapter) => chapter.id === chapterId,
            );
            const section = chapter?.sections.find(
                (section) => section.id === sectionId,
            );
            history.push(
                `/courses/${courseId}/chapters/${chapter?.id}/sections/${section.id}/edit`,
            );
        },
        [chapters, courseId, history],
    );

    return (
        <>
            <div className={classes.root}>
                {(chapters.length ?? 0) > 0 && (
                    <>
                        {chapters.map((chapter, index) => (
                            <CourseChapter
                                key={chapter.id}
                                id={chapter.id}
                                index={index + 1}
                                title={chapter.title}
                                sections={chapter.sections}
                                onReorderSections={handleReorderSections}
                                onEditChapter={handleEditChapter}
                                onDeleteChapter={handleDeleteChapter}
                                onSaveSection={handleSaveSection}
                                onDeleteSection={handleDeleteSection}
                                onEdit={handleAddContent}
                            />
                        ))}
                        <div className={classes.secondaryActions}>
                            <ButtonBase
                                onClick={handleNewChapter}
                                className={classes.newChapter}>
                                <Icon className={classes.newChapterIcon}>
                                    add_circle
                                </Icon>
                                <Typography className={classes.newChapterTitle}>
                                    New Chapter
                                </Typography>
                            </ButtonBase>
                        </div>
                    </>
                )}
                {chapters.length === 0 && (
                    <div className={classes.noRecords}>
                        <NoRecords
                            message={`Are you ready to create your first chapter, ${firstName}?`}
                            actionText="Create New Chapter"
                            actionHandler={handleNewChapter}
                            image={`${process.env.PUBLIC_URL}/assets/images/empty-articles.svg`}
                            action={true}
                        />
                    </div>
                )}
            </div>

            <ChapterEditor
                open={openChapterEditor}
                onClose={handleCloseChapterEditor}
                onSave={handleSaveChapter}
                id={currentChapter?.id}
                title={currentChapter?.title}
                description={currentChapter?.description}
            />
        </>
    );
};

export default CourseCurriculum;
