import {
    FunctionComponent,
    useRef,
    useEffect,
    useState,
    useCallback,
} from "react";
import {
    Drawer,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    Button,
    Typography,
    Toolbar,
    AppBar,
    CircularProgress,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Formsy from "formsy-react";

import { CouponDuration, Coupon } from "../../types";
import { useAppSelector } from "../../redux/hooks";
import TextField from "../common/FormsyTextField";

const useStyles = makeStyles((theme) => ({
    root: {
        width: 440,
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-between",
        minHeight: "100vh",
        backgroundColor: (theme.palette.background as any).level1,
    },
    top: {},
    toolbar: {
        backgroundColor: (theme.palette.background as any).level2,
    },
    toolbarContent: {
        width: "100%",
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        alignItems: "center",
    },
    actions: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-end",
        alignItems: "center",
        padding: theme.spacing(2),
    },
    form: {
        padding: theme.spacing(2),
        marginTop: theme.spacing(2),
        display: "flex",
        flexDirection: "column",
    },
    title: {
        fontWeight: "bold",
        fontSize: 20,
        maxWidth: 800,
    },
    name: {
        marginTop: theme.spacing(2),
    },
    code: {
        marginTop: theme.spacing(2),
    },
    discount: {
        marginTop: theme.spacing(2),
    },
    duration: {
        marginTop: theme.spacing(2),
    },
    maxRedemptions: {
        marginTop: theme.spacing(2),
    },
    action: {
        width: 96,
        marginLeft: theme.spacing(1),
    },
    actionIcon: {
        marginRight: theme.spacing(1),
    },
    progress: {
        marginRight: theme.spacing(1),
    },
}));

const durations = [
    {
        title: "Three Months",
        value: "three_months",
    },
    {
        title: "Forever",
        value: "forever",
    },
];

export interface Props {
    id?: string;
    name?: string;
    code?: string;
    discount?: number;
    duration?: CouponDuration;
    active?: boolean;
    maxRedemptions?: number;
    open: boolean;
    onClose: () => void;
    onSave: (coupon: Coupon) => void;
}

const SectionEditor: FunctionComponent<Props> = (props: Props) => {
    const { id, open, onClose, onSave } = props;
    const classes = useStyles();
    const [name, setName] = useState(props.name || "");
    const [code, setCode] = useState(props.code || "");
    const [discount, setDiscount] = useState(props.discount || 0);
    const [duration, setDuration] = useState(props.duration || "forever");
    const [active, setActive] = useState(props.active || false);
    const [maxRedemptions, setMaxRedemptions] = useState(
        props.maxRedemptions || 1,
    );
    const [valid, setValid] = useState(false);
    const previousCreating = useRef(false);
    const previousUpdating = useRef(false);
    const { creating, updating } = {};

    const changesMade =
        !id ||
        props.name !== name ||
        props.code !== code ||
        props.discount !== discount ||
        props.duration !== duration ||
        props.active !== active ||
        props.maxRedemptions !== maxRedemptions;

    useEffect(() => {
        setName(props.name || "");
        setCode(props.code || "");
        setDiscount(props.discount || 0);
        setDuration(props.duration || "forever");
        setActive(props.active || false);
        setMaxRedemptions(props.maxRedemptions || 1);
    }, [
        props.active,
        props.code,
        props.discount,
        props.duration,
        props.maxRedemptions,
        props.name,
    ]);

    useEffect(() => {
        if (previousCreating.current && !creating) {
            onClose();
        }

        previousCreating.current = creating;
    }, [creating, onClose]);

    useEffect(() => {
        if (previousUpdating.current && !updating) {
            onClose();
        }

        previousUpdating.current = updating;
    }, [updating, onClose]);

    const handleNameChange = useCallback((event) => {
        setName(event.target.value);
    }, []);

    const handleCodeChange = useCallback((event) => {
        setCode(event.target.value);
    }, []);

    const handleDiscountChange = useCallback((event) => {
        setDiscount(event.target.value);
    }, []);

    const handleDurationChange = useCallback((event) => {
        setDuration(event.target.value);
    }, []);

    const handleActiveChange = useCallback((event) => {
        setActive(event.target.value);
    }, []);

    const handleMaxRedemptionsChange = useCallback((event) => {
        setMaxRedemptions(parseInt(event.target.value, 10) || 1);
    }, []);

    const handleValidForm = useCallback(() => {
        setValid(true);
    }, []);

    const handleInvalidForm = useCallback(() => {
        setValid(false);
    }, []);

    const handleSave = useCallback(() => {
        onSave({
            id,
            name,
            code,
            discount,
            duration,
            active,
            maxRedemptions,
        });
    }, [active, code, discount, duration, id, maxRedemptions, name, onSave]);

    return (
        <Drawer anchor="right" open={open} onClose={onClose}>
            <div className={classes.root}>
                <div className={classes.top}>
                    <AppBar position="sticky">
                        <Toolbar className={classes.toolbar}>
                            <div className={classes.toolbarContent}>
                                <Typography className={classes.title}>
                                    {id ? "Edit Coupon" : "New Coupon"}
                                </Typography>
                            </div>
                        </Toolbar>
                    </AppBar>
                    <Formsy
                        className={classes.form}
                        onValid={handleValidForm}
                        onInvalid={handleInvalidForm}>
                        <TextField
                            name="name"
                            className={classes.name}
                            label="Name"
                            validations={{
                                minLength: 4,
                                maxLength: 48,
                            }}
                            validationErrors={{
                                minLength:
                                    "The name should have 4-48 characters",
                                maxLength:
                                    "The name should have 4-48 characters",
                            }}
                            variant="outlined"
                            size="medium"
                            value={name}
                            onChange={handleNameChange}
                            required={true}
                            fullWidth={true}
                        />
                        <TextField
                            name="code"
                            className={classes.code}
                            label="Code"
                            validations={{
                                minLength: 4,
                                maxLength: 16,
                            }}
                            validationErrors={{
                                minLength:
                                    "The code should have 4-16 characters",
                                maxLength:
                                    "The code should have 4-16 characters",
                            }}
                            variant="outlined"
                            size="medium"
                            value={code}
                            onChange={handleCodeChange}
                            required={true}
                            fullWidth={true}
                        />
                        <TextField
                            name="discount"
                            className={classes.discount}
                            label="Discount"
                            validations={{
                                isNumeric: true,
                            }}
                            validationErrors={{
                                isNumeric: "Enter a valid discount",
                            }}
                            variant="outlined"
                            size="medium"
                            value={discount}
                            onChange={handleDiscountChange}
                            required={true}
                            fullWidth={true}
                        />
                        <FormControl
                            variant="outlined"
                            className={classes.duration}
                            size="medium"
                            required={true}>
                            <InputLabel id="durationLabel">Duration</InputLabel>
                            <Select
                                labelId="durationLabel"
                                value={duration}
                                onChange={handleDurationChange}
                                label="Duration">
                                {durations.map((duration) => (
                                    <MenuItem
                                        key={duration.value}
                                        value={duration.value}>
                                        {duration.title}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                        <TextField
                            name="maxRedemptions"
                            className={classes.maxRedemptions}
                            label="Max Redemptions"
                            validations={{
                                isInt: true,
                            }}
                            validationErrors={{
                                isInt: "Enter a valid redemption limit",
                            }}
                            variant="outlined"
                            size="medium"
                            value={maxRedemptions}
                            onChange={handleMaxRedemptionsChange}
                            required={true}
                            fullWidth={true}
                        />
                    </Formsy>
                </div>
                <div className={classes.actions}>
                    <Button
                        variant="outlined"
                        className={classes.action}
                        onClick={onClose}>
                        Cancel
                    </Button>
                    <Button
                        variant="contained"
                        className={classes.action}
                        disabled={
                            !changesMade || !valid || creating || updating
                        }
                        onClick={handleSave}>
                        {(creating || updating) && (
                            <CircularProgress
                                size="16px"
                                color="inherit"
                                className={classes.progress}
                            />
                        )}
                        Save
                    </Button>
                </div>
            </div>
        </Drawer>
    );
};

export default SectionEditor;
