import { FunctionComponent, useState, useCallback, useEffect } from "react";
import {
    Card,
    CardContent,
    CardActions,
    Icon,
    Button,
    CircularProgress,
    Typography,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Formsy from "formsy-react";

import TextField from "../common/FormsyTextField";

const useStyles = makeStyles((theme) => ({
    card: {
        width: 700,
        padding: theme.spacing(1),
    },
    content: {},
    question: {
    },
    form: {
        display: "flex",
        flexDirection: "column",
    },
    answer: {
        marginTop: theme.spacing(1),
        marginBottom: theme.spacing(4),
    },
    actions: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-end",
        width: "100%",
    },
    action: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2),
        width: 160,
    },
    actionIcon: {
        marginRight: theme.spacing(1),
    },
    progress: {
        marginRight: theme.spacing(1),
    },
}));

interface Props {
    id: string;
    objectives: string;
    requirements: string;
    targets: string;
    resources: string;
    updating: boolean;
    onUpdate: (update: any) => void;
}

const CourseAudience: FunctionComponent<Props> = (props: Props) => {
    const { id: courseId, updating, onUpdate } = props;
    const classes = useStyles();
    const [objectives, setObjectives] = useState("");
    const [requirements, setRequirements] = useState("");
    const [targets, setTargets] = useState("");
    const [resources, setResources] = useState("");
    const [valid, setValid] = useState(false);

    useEffect(() => {
        setObjectives(props.objectives);
        setRequirements(props.requirements);
        setTargets(props.targets);
        setResources(props.resources);
    }, [props.objectives, props.requirements, props.resources, props.targets]);

    const handleObjectivesChange = useCallback((event) => {
        setObjectives(event.target.value);
    }, []);

    const handleRequirementsChange = useCallback((event) => {
        setRequirements(event.target.value);
    }, []);

    const handleTargetsChange = useCallback((event) => {
        setTargets(event.target.value);
    }, []);

    const handleResourcesChange = useCallback((event) => {
        setResources(event.target.value);
    }, []);

    const handleValidForm = useCallback(() => {
        setValid(true);
    }, []);

    const handleInvalidForm = useCallback(() => {
        setValid(false);
    }, []);

    const handleSave = useCallback(
        () => {
            onUpdate({
                courseId,
                requirements,
                objectives,
                targets,
                resources
            });
        },
        [courseId, objectives, onUpdate, requirements, resources, targets],
    );

    return (
        <Card className={classes.card}>
            <CardContent className={classes.content}>
                <Formsy
                    className={classes.form}
                    onValid={handleValidForm}
                    onInvalid={handleInvalidForm}>
                    <Typography className={classes.question}>
                        What will students learn in your course?
                    </Typography>
                    <TextField
                        name="objectives"
                        className={classes.answer}
                        validations={{
                            maxLength: 2048,
                        }}
                        validationErrors={{
                            maxLength:
                                "The objective should not be longer than 2048 characters",
                        }}
                        variant="outlined"
                        size="medium"
                        value={objectives}
                        onChange={handleObjectivesChange}
                        multiline={true}
                        rows={5}
                    />

                    <Typography className={classes.question}>
                        Are there any course requirements or prerequisites?
                    </Typography>
                    <TextField
                        name="requirements"
                        className={classes.answer}
                        validations={{
                            maxLength: 2048,
                        }}
                        validationErrors={{
                            maxLength:
                                "The requirements should not be longer than 2048 characters",
                        }}
                        variant="outlined"
                        size="medium"
                        value={requirements}
                        onChange={handleRequirementsChange}
                        multiline={true}
                        rows={5}
                    />

                    <Typography className={classes.question}>
                        Who are your target students?
                    </Typography>
                    <TextField
                        name="objective"
                        className={classes.answer}
                        validations={{
                            maxLength: 2048,
                        }}
                        validationErrors={{
                            maxLength:
                                "The targets should not be longer than 2048 characters",
                        }}
                        variant="outlined"
                        size="medium"
                        value={targets}
                        onChange={handleTargetsChange}
                        multiline={true}
                        rows={5}
                    />

                    <Typography className={classes.question}>
                        What resources will students have access to?
                    </Typography>
                    <TextField
                        name="objective"
                        className={classes.answer}
                        validations={{
                            maxLength: 2048,
                        }}
                        validationErrors={{
                            maxLength:
                                "The resources should not be longer than 2048 characters",
                        }}
                        variant="outlined"
                        size="medium"
                        value={resources}
                        onChange={handleResourcesChange}
                        multiline={true}
                        rows={5}
                    />
                </Formsy>
            </CardContent>
            <CardActions className={classes.actions}>
                <Button
                    className={classes.action}
                    variant="contained"
                    color="primary"
                    disabled={objectives === "" || !valid || updating}
                    onClick={handleSave}>
                    {!updating && (
                        <>
                            <Icon
                                className={classes.actionIcon}
                                fontSize="small">
                                save
                            </Icon>
                            Save
                        </>
                    )}
                    {updating && (
                        <>
                            <CircularProgress
                                size="16px"
                                color="inherit"
                                className={classes.progress}
                            />
                            Saving...
                        </>
                    )}
                </Button>
            </CardActions>
        </Card>
    );
};

export default CourseAudience;
