import { useState, useCallback, useEffect, FunctionComponent } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Formsy from "formsy-react";
import {
    Card,
    CardContent,
    Button,
    CircularProgress,
    Icon,
    Typography,
} from "@material-ui/core";
import lodash from "lodash";

import { Question, QuestionType, QuestionOption } from "../../types";
import TextField from "../common/FormsyTextField";
import QuestionEditor from "./QuestionEditor";

const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 800,
    },
    title: {},
    description: {
        marginTop: theme.spacing(2),
    },
    actions: {
        marginTop: theme.spacing(2),
        width: "100%",
        display: "flex",
        justifyContent: "flex-end",
        flexDirection: "row",
    },
    action: {
        width: 128,
        marginTop: theme.spacing(2),
    },
    actionIcon: {
        marginRight: theme.spacing(1),
    },
    progress: {
        marginRight: theme.spacing(1),
    },
    secondaryActions: {
        display: "flex",
        justifyContent: "center",
        width: "100%",
    },
    newQuestion: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2),
        paddingTop: theme.spacing(1),
        paddingBottom: theme.spacing(1),
        marginTop: theme.spacing(2),
        alignSelf: "center",
        borderRadius: theme.shape.borderRadius,
    },
    newQuestionIcon: {
        color: "white",
        marginRight: theme.spacing(1),
        fontSize: 18,
    },
    newQuestionTitle: { color: "white", fontSize: 14 },
}));

const EMPTY_QUESTION: Question = {
    text: "",
    type: "single_correct_option",
    options: [
        {
            text: "",
            correct: true,
        },
        {
            text: "",
            correct: false,
        },
    ],
};

interface Props {
    title: string;
    description: string;
    questions: Question[];
    updating: boolean;
    onSave: (title: string, description: string, questions: Question[]) => void;
}

const QuizEditor: FunctionComponent<Props> = (props: Props) => {
    const { onSave, updating } = props;
    const classes = useStyles();
    const [title, setTitle] = useState("");
    const [description, setDescription] = useState("");
    const [questions, setQuestions] = useState<Question[]>([EMPTY_QUESTION]);
    const [valid, setValid] = useState(false);

    useEffect(() => {
        setDescription(props.description);
        setTitle(props.title);
        setQuestions(props.questions);
    }, [props.description, props.title, props.questions]);

    const handleTitleChange = useCallback((event) => {
        setTitle(event.target.value);
    }, []);

    const handleDescriptionChange = useCallback((event) => {
        setDescription(event.target.value);
    }, []);

    const handleValidForm = useCallback(() => {
        setValid(true);
    }, []);

    const handleInvalidForm = useCallback(() => {
        setValid(false);
    }, []);

    /* NOTE: We do not make deep clones of questions. Since the question objects
     * are immutable, whenever a question is changed, we clone only that particular
     * question. A new questions array is created and all the remaining questions
     * are carried over.
     */

    const handleQuestionTextChange = useCallback(
        (index: number, text: string) => {
            const newQuestion = lodash.cloneDeep(questions[index]);
            newQuestion.text = text;

            const newQuestions = [...questions];
            newQuestions[index] = newQuestion;
            setQuestions(newQuestions);
        },
        [questions, setQuestions],
    );

    const handleQuestionTypeChange = useCallback(
        (index: number, type: QuestionType) => {
            const newQuestion = lodash.cloneDeep(questions[index]);
            newQuestion.type = type;
            for (const option of newQuestion.options) {
                option.correct = false;
            }
            newQuestion.options[0].correct = true;

            const newQuestions = [...questions];
            newQuestions[index] = newQuestion;
            setQuestions(newQuestions);
        },
        [questions, setQuestions],
    );

    const handleQuestionOptionsChange = useCallback(
        (index: number, options: QuestionOption[]) => {
            const newQuestion = lodash.cloneDeep(questions[index]);
            newQuestion.options = options;

            const newQuestions = [...questions];
            newQuestions[index] = newQuestion;
            setQuestions(newQuestions);
        },
        [questions, setQuestions],
    );

    const handleNewQuestion = useCallback(
        (index: number) => {
            const newQuestions = [
                ...questions.slice(0, index + 1),
                EMPTY_QUESTION,
                ...questions.slice(index + 1),
            ];
            setQuestions(newQuestions);
        },
        [questions],
    );

    const handleDuplicateQuestion = useCallback(
        (index: number) => {
            const duplicateQuestion = lodash.cloneDeep(questions[index]);
            const newQuestions = [
                ...questions.slice(0, index + 1),
                duplicateQuestion,
                ...questions.slice(index + 1),
            ];
            setQuestions(newQuestions);
        },
        [questions],
    );

    const handleDeleteQuestion = useCallback(
        (index: number) => {
            const newQuestions = [
                ...questions.slice(0, index),
                ...questions.slice(index + 1),
            ];
            setQuestions(newQuestions);
        },
        [questions],
    );

    const handleAddQuestion = useCallback(() => {
        handleNewQuestion(0);
    }, [handleNewQuestion]);

    const handleSave = useCallback(() => {
        onSave(title, description, questions);
    }, [description, onSave, questions, title]);

    const changesMade =
        props.title !== title ||
        props.description !== description ||
        !lodash.isEqual(props.questions, questions);

    return (
        <div className={classes.root}>
            <Card>
                <CardContent>
                    <Formsy
                        onValid={handleValidForm}
                        onInvalid={handleInvalidForm}>
                        <TextField
                            name="title"
                            className={classes.title}
                            label="Title"
                            validations={{
                                minLength: 8,
                                maxLength: 504,
                            }}
                            validationErrors={{
                                minLength:
                                    "The title should have 8-504 characters",
                                maxLength:
                                    "The title should have 8-504 characters",
                            }}
                            variant="outlined"
                            size="medium"
                            value={title}
                            onChange={handleTitleChange}
                            required={true}
                            fullWidth={true}
                        />
                        <TextField
                            name="description"
                            className={classes.description}
                            label="Description"
                            validations={{
                                maxLength: 1024,
                            }}
                            validationErrors={{
                                maxLength:
                                    "The description cannot be longer than 1024 characters",
                            }}
                            variant="outlined"
                            size="medium"
                            value={description}
                            onChange={handleDescriptionChange}
                            fullWidth={true}
                            multiline={true}
                            rows={5}
                        />
                    </Formsy>
                </CardContent>
            </Card>
            {questions.map((question, index) => (
                <QuestionEditor
                    index={index}
                    text={question.text}
                    type={question.type}
                    options={question.options}
                    onTextChange={handleQuestionTextChange}
                    onTypeChange={handleQuestionTypeChange}
                    onOptionsChange={handleQuestionOptionsChange}
                    onNew={handleNewQuestion}
                    onDuplicate={handleDuplicateQuestion}
                    onDelete={handleDeleteQuestion}
                />
            ))}
            {questions.length === 0 && (
                <div className={classes.secondaryActions}>
                    <Button
                        onClick={handleAddQuestion}
                        className={classes.newQuestion}>
                        <Icon className={classes.newQuestionIcon}>
                            add_circle
                        </Icon>
                        <Typography className={classes.newQuestionTitle}>
                            New Question
                        </Typography>
                    </Button>
                </div>
            )}
            <div className={classes.actions}>
                <Button
                    variant="contained"
                    className={classes.action}
                    disabled={!changesMade || !valid || updating}
                    onClick={handleSave}>
                    {updating && (
                        <CircularProgress
                            size="16px"
                            color="inherit"
                            className={classes.progress}
                        />
                    )}
                    {!updating && (
                        <Icon className={classes.actionIcon}>save</Icon>
                    )}
                    Save
                </Button>
            </div>
        </div>
    );
};

export default QuizEditor;
