import {
    FunctionComponent,
    useState,
    useCallback,
    useRef,
    useEffect,
} from "react";
import {
    Drawer,
    Button,
    Typography,
    CircularProgress,
    AppBar,
    Toolbar,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Formsy from "formsy-react";

import TextField from "../common/FormsyTextField";
import { Chapter } from "../../types";

const useStyles = makeStyles((theme) => ({
    root: {
        width: 800,
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-between",
        minHeight: "100vh",
    },
    toolbar: {
        backgroundColor: (theme.palette.background as any).level2,
    },
    toolbarContent: {
        width: "100%",
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        alignItems: "center",
    },
    top: {
        width: "100%",
        display: "flex",
        flexDirection: "column",
    },
    bottom: {
        width: "100%",
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-end",
        alignItems: "center",
        padding: theme.spacing(2),
    },
    form: {
        display: "flex",
        flexDirection: "column",
        marginTop: theme.spacing(3),
        padding: theme.spacing(2),
    },
    title: {
        fontWeight: "bold",
        fontSize: 20,
    },
    description: {
        marginTop: theme.spacing(2),
    },
    type: {
        marginTop: theme.spacing(2),
        width: 320,
    },
    action: {
        width: 96,
        marginLeft: theme.spacing(1),
    },
    progress: {
        marginRight: theme.spacing(1),
    },
}));

export interface Props {
    id?: string;
    title?: string;
    description?: string;
    open: boolean;
    onClose: () => void;
    onSave: (chapter: Chapter) => void;
}

const ChapterEditor: FunctionComponent<Props> = (props: Props) => {
    const { id: chapterId, open, onClose, onSave } = props;
    const classes = useStyles();
    const [title, setTitle] = useState(props.title || "");
    const [description, setDescription] = useState(props.description || "");
    const [valid, setValid] = useState(false);
    const previousCreating = useRef(false);
    const previousUpdating = useRef(false);
    // TODO: Update this!
    const creating = false, updating = false;

    const changesMade =
        !chapterId ||
        props.title !== title ||
        props.description !== description;

    useEffect(() => {
        setTitle(props.title || "");
        setDescription(props.description || "");
    }, [props.title, props.description]);

    useEffect(() => {
        if (previousCreating.current && !creating) {
            onClose();
        }

        previousCreating.current = creating;
    }, [creating, onClose]);

    useEffect(() => {
        if (previousUpdating.current && !updating) {
            onClose();
        }

        previousUpdating.current = updating;
    }, [updating, onClose]);

    const handleTitleChange = useCallback((event) => {
        setTitle(event.target.value);
    }, []);

    const handleDescriptionChange = useCallback((event) => {
        setDescription(event.target.value);
    }, []);

    const handleValidForm = useCallback(() => {
        setValid(true);
    }, []);

    const handleInvalidForm = useCallback(() => {
        setValid(false);
    }, []);

    const handleSave = useCallback(() => {
        onSave({
            chapterId,
            title,
            description,
        });
    }, [description, chapterId, onSave, title]);

    return (
        <Drawer anchor="right" open={open} onClose={onClose}>
            <div className={classes.root}>
                <div className={classes.top}>
                    <AppBar position="sticky">
                        <Toolbar className={classes.toolbar}>
                            <div className={classes.toolbarContent}>
                                <Typography className={classes.title}>
                                    {chapterId ? "Edit Chapter" : "New Chapter"}
                                </Typography>
                            </div>
                        </Toolbar>
                    </AppBar>
                    <Formsy
                        className={classes.form}
                        onValid={handleValidForm}
                        onInvalid={handleInvalidForm}>
                        <TextField
                            name="title"
                            className={classes.title}
                            label="Title"
                            validations={{
                                minLength: 16,
                                maxLength: 504,
                            }}
                            validationErrors={{
                                minLength:
                                    "The title should have 16-504 characters",
                                maxLength:
                                    "The title should have 16-504 characters",
                            }}
                            variant="outlined"
                            size="medium"
                            value={title}
                            onChange={handleTitleChange}
                            required={true}
                            fullWidth={true}
                        />
                        <TextField
                            name="description"
                            className={classes.description}
                            label="Description"
                            validations={{
                                maxLength: 1024,
                            }}
                            validationErrors={{
                                maxLength:
                                    "The description cannot be longer than 1024 characters",
                            }}
                            variant="outlined"
                            size="medium"
                            value={description}
                            onChange={handleDescriptionChange}
                            fullWidth={true}
                            multiline={true}
                            rows={10}
                        />
                    </Formsy>
                </div>
                <div className={classes.bottom}>
                    <Button
                        variant="outlined"
                        className={classes.action}
                        onClick={onClose}>
                        Cancel
                    </Button>
                    <Button
                        variant="contained"
                        className={classes.action}
                        disabled={
                            !changesMade || !valid || creating || updating
                        }
                        onClick={handleSave}>
                        {(creating || updating) && (
                            <CircularProgress
                                size="16px"
                                color="inherit"
                                className={classes.progress}
                            />
                        )}
                        Save
                    </Button>
                </div>
            </div>
        </Drawer>
    );
};

export default ChapterEditor;
