import { FunctionComponent, useState, useCallback, useEffect } from "react";
import {
    Card,
    CardContent,
    CardActions,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    Icon,
    Button,
    CircularProgress,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Formsy from "formsy-react";
import currencyCodes from "currency-codes";

import TextField from "../common/FormsyTextField";
import { Course } from "../../types";

const currencyCodesList = [...currencyCodes.data]
    .sort((a, b) => a.currency.localeCompare(b.currency))
    .map((item) => item.code);

const useStyles = makeStyles((theme) => ({
    card: {
        width: 700,
    },
    content: {
        padding: theme.spacing(2),
    },
    form: {
        display: "flex",
        flexDirection: "column",
    },
    currencies: {},
    actualPrice: {
        marginTop: theme.spacing(3),
    },
    discount: {
        marginTop: theme.spacing(3),
    },
    discountedPrice: {
        marginTop: theme.spacing(3),
    },
    currency: {
        marginTop: theme.spacing(3),
    },
    actions: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-end",
        width: "100%",
        padding: theme.spacing(2),
    },
    action: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2),
        width: 128,
    },
    actionIcon: {
        marginRight: theme.spacing(1),
    },
    progress: {
        marginRight: theme.spacing(1),
    },
}));

interface Params {
    id: string;
}

interface Props {
    id: string;
    actualPrice: number;
    discountedPrice: number;
    onUpdate: (course: any) => void;
    updating: boolean;
}

// TODO: The currency is currently not saved in the database.
const CoursePricing: FunctionComponent<Props> = (props: Props) => {
    const {
        id: courseId,
        actualPrice: actualPrice0,
        discountedPrice: discountedPrice0,
        onUpdate,
        updating,
    } = props;
    const classes = useStyles();
    const [actualPrice, setActualPrice] = useState(0);
    const [discount, setDiscount] = useState(0);
    const [discountedPrice, setDiscountedPrice] = useState(0);
    const [currency, setCurrency] = useState("INR");
    const [valid, setValid] = useState(false);

    const changesMade =
        props.actualPrice !== actualPrice ||
        props.discountedPrice !== discountedPrice;
    /*props.currency !== currency*/

    /* Propogate changes in props to our local state. */
    useEffect(() => {
        setActualPrice(actualPrice0 || 0);
        setDiscountedPrice(discountedPrice0 || 9);
        setDiscount(
            ((actualPrice0 - discountedPrice0) / actualPrice0) * 100 || 0,
        );
    }, [actualPrice0, discountedPrice0, props.actualPrice, props.discountedPrice]);

    const handleActualPriceChange = useCallback(
        (event) => {
            const newActualPrice = parseFloat(event.target.value) || 0;
            const newDiscountedPrice =
                newActualPrice - (newActualPrice * discount) / 100;
            setActualPrice(newActualPrice);
            setDiscountedPrice(newDiscountedPrice);
        },
        [discount],
    );

    const handleDiscountChange = useCallback(
        (event) => {
            const newDiscount = parseFloat(event.target.value) || 0;
            const discountAmount = (newDiscount / 100) * actualPrice;
            const newDiscountedPrice = actualPrice - discountAmount;
            setDiscount(newDiscount);
            setDiscountedPrice(newDiscountedPrice);
        },
        [actualPrice],
    );

    const handleDiscountedPriceChange = useCallback(
        (event) => {
            const newDiscountedPrice = parseFloat(event.target.value) || 0;
            const newDiscount =
                ((actualPrice - newDiscountedPrice) / actualPrice) * 100;
            setDiscount(newDiscount);
            setDiscountedPrice(newDiscountedPrice);
        },
        [actualPrice],
    );

    const handleCurrencyChange = useCallback((event, newCurrency) => {
        setCurrency(newCurrency);
    }, []);

    const handleValidForm = useCallback(() => {
        setValid(true);
    }, []);

    const handleInvalidForm = useCallback(() => {
        setValid(false);
    }, []);

    const handleSubmit = useCallback(() => {
        onUpdate({
            courseId,
            actualPrice,
            discountedPrice,
        });
    }, [actualPrice, courseId, discountedPrice, onUpdate]);

    return (
        <Card className={classes.card}>
            <CardContent className={classes.content}>
                <Formsy
                    className={classes.form}
                    onValid={handleValidForm}
                    onInvalid={handleInvalidForm}>
                    <FormControl
                        variant="outlined"
                        className={classes.currencies}
                        size="medium"
                        required={true}>
                        <InputLabel id="currencyLabel">Currency</InputLabel>
                        <Select
                            labelId="currencyLabel"
                            value={currency}
                            onChange={handleCurrencyChange}
                            label="Currency">
                            {currencyCodesList.map((currencyCode, index) => {
                                const currency =
                                    currencyCodes.code(currencyCode);
                                return (
                                    <MenuItem
                                        key={currency?.code ?? index}
                                        value={currency?.code ?? index}>
                                        {currency?.currency} ({currency?.code})
                                    </MenuItem>
                                );
                            })}
                        </Select>
                    </FormControl>
                    <TextField
                        name="actualPrice"
                        className={classes.actualPrice}
                        label="Actual Price"
                        validations={{
                            isNumeric: true,
                        }}
                        validationErrors={{
                            isNumeric: "Enter a valid price",
                        }}
                        variant="outlined"
                        size="medium"
                        value={actualPrice}
                        onChange={handleActualPriceChange}
                        required={true}
                    />
                    <TextField
                        name="discount"
                        className={classes.discount}
                        label="Discount"
                        validations={{
                            isNumeric: true,
                        }}
                        validationErrors={{
                            isNumeric: "Enter a valid discount",
                        }}
                        variant="outlined"
                        size="medium"
                        value={discount}
                        onChange={handleDiscountChange}
                    />
                    <TextField
                        name="discountedPrice"
                        className={classes.discountedPrice}
                        label="Discounted Price"
                        validations={{
                            isNumeric: true,
                        }}
                        validationErrors={{
                            isNumeric: "Enter a valid price",
                        }}
                        variant="outlined"
                        size="medium"
                        value={discountedPrice}
                        onChange={handleDiscountedPriceChange}
                    />
                </Formsy>
            </CardContent>
            <CardActions className={classes.actions}>
                <Button
                    className={classes.action}
                    variant="contained"
                    color="primary"
                    disabled={!changesMade || !valid || updating}
                    onClick={handleSubmit}>
                    {!updating && (
                        <Icon className={classes.actionIcon} fontSize="small">
                            save
                        </Icon>
                    )}
                    {updating && (
                        <CircularProgress
                            size="16px"
                            color="inherit"
                            className={classes.progress}
                        />
                    )}
                    Save
                </Button>
            </CardActions>
        </Card>
    );
};

export default CoursePricing;
