import {
    FunctionComponent,
    useEffect,
    useRef,
    useCallback,
    useMemo,
} from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
    CircularProgress,
    AppBar,
    Button,
    Toolbar,
    Typography,
    Breadcrumbs,
    Link,
    Icon,
} from "@material-ui/core";
import { Redirect, Route, Switch } from "react-router";
import { useHistory, useParams } from "react-router-dom";
import { gql, useLazyQuery, useMutation } from "@apollo/client";
import { useConfirm } from "material-ui-confirm";

import CourseNavigation from "./CourseNavigation";
import CourseDetails from "./CourseDetails";
import CourseAudience from "./CourseAudience";
import CoursePricing from "./CoursePricing";
import CourseCurriculum from "./CourseCurriculum";
import CoursePromotions from "./CoursePromotions";
import ContentEditor from "./ContentEditor";

const useStyles = makeStyles((theme) => ({
    suspense: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        minHeight: "100vh",
        backgroundColor: theme.palette.background.default,
        width: "100%",
    },
    root: {
        minHeight: "100vh",
        backgroundColor: theme.palette.background.default,
        width: "100%",
        display: "flex",
        flexDirection: "column",
        justifyContent: "flex-start",
    },
    top: {},
    bottom: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-start",
        padding: theme.spacing(4),
    },
    content: {},
    title: {
        fontWeight: "bold",
        fontSize: 20,
    },
    description: {
        marginTop: theme.spacing(2),
        fontWeight: 400,
    },
    toolbar: {
        backgroundColor: (theme.palette.background as any).level2,
    },
    toolbarContent: {
        width: "100%",
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        alignItems: "center",
    },
    toolbarActions: {
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        justifyContent: "center",
    },
    action: {
        paddingLeft: theme.spacing(1),
        paddingRight: theme.spacing(1),
        marginLeft: theme.spacing(1),
    },
    actionIcon: {
        marginRight: theme.spacing(1),
    },
    toolbarTitle: {
        fontWeight: "bold",
        fontSize: 20,
        maxWidth: 800,
    },
    link: {
        cursor: "pointer",
    },
    navigation: {
        width: 260,
    },
    container: {
        flexGrow: 1,
        paddingLeft: theme.spacing(5),
        paddingRight: theme.spacing(5),
    },
}));

interface Params {
    id: string;
}

const GET_COURSE = gql`
    query GetCourse($courseId: ID!) {
        getCourseById(courseId: $courseId) {
            id
            title
            description
            level
            languageCode
            status
            objectives
            requirements
            targets
            resources
            actualPrice
            discountedPrice
            chapters {
                id
                title
                sections {
                    id
                    type
                    title
                }
            }
        }
    }
`;

const CREATE_EMPTY_COURSE = gql`
    mutation CreateEmptyCourse {
        createCourse {
            id
        }
    }
`;

const UPDATE_COURSE_DETAILS = gql`
    mutation UpdateCourse(
        $courseId: ID!
        $title: String
        $description: String
        $level: CourseLevel
        $imageURL: String
        $languageCode: Language
        $linear: Boolean
        $actualPrice: Float
        $discountedPrice: Float
        $requirements: String
        $objectives: String
        $targets: String
        $resources: String
        $chapters: [String!]
    ) {
        updateCourse(
            courseId: $courseId
            title: $title
            description: $description
            level: $level
            imageURL: $imageURL
            languageCode: $languageCode
            linear: $linear
            actualPrice: $actualPrice
            discountedPrice: $discountedPrice
            requirements: $requirements
            objectives: $objectives
            targets: $targets
            chapters: $chapters
            resources: $resources
        ) {
            id
            title
            description
            level
            imageURL
            languageCode
            linear
            actualPrice
            discountedPrice
            requirements
            objectives
            targets
            chapters {
                id
            }
            resources
        }
    }
`;

const PUBLISH_COURSE = gql`
    mutation PublishCourse($courseId: ID!) {
        publishCourse(courseId: $courseId) {
            status
        }
    }
`;

const DELETE_COURSE = gql`
    mutation DeleteCourse($courseId: ID!) {
        deleteCourse(courseId: $courseId)
    }
`;

const UNPUBLISH_COURSE = gql`
    mutation UnpublishCourse($courseId: ID!) {
        unpublishCourse(courseId: $courseId) {
            status
        }
    }
`;

const CREATE_CHAPTER = gql`
    mutation CreateChapter(
        $title: String
        $courseId: String!
        $description: String
        $sections: [String!]
    ) {
        createChapter(
            title: $title
            course: $courseId
            description: $description
            sections: $sections
        ) {
            id
        }
    }
`;

const UPDATE_CHAPTER = gql`
    mutation UpdateChapter(
        $chapterId: ID!
        $title: String
        $description: String
        $sections: [String!]
    ) {
        updateChapter(
            chapterId: $chapterId
            title: $title
            description: $description
            sections: $sections
        ) {
            id
        }
    }
`;

const DELETE_CHAPTER = gql`
    mutation DeleteChapter($chapterId: ID!) {
        deleteChapter(chapterId: $chapterId)
    }
`;

const CREATE_SECTION = gql`
    mutation CreateSection(
        $chapterId: ID!
        $title: String
        $type: SectionType!
        $description: String
    ) {
        createSection(
            chapter: $chapterId
            title: $title
            type: $type
            description: $description
        ) {
            id
        }
    }
`;

const DELETE_SECTION = gql`
    mutation DeleteSection($sectionId: ID!) {
        deleteSection(sectionId: $sectionId)
    }
`;

const CourseEditor: FunctionComponent = () => {
    const params = useParams<Params>();
    const history = useHistory();
    const classes = useStyles();
    const [
        getCourse,
        { loading: fetching, error: currentError, data: currentCourseData },
    ] = useLazyQuery(GET_COURSE);
    // TODO: Courses must be updated!
    const [
        createEmptyCourse,
        { loading: creating, error: newError, data: newCourse },
    ] = useMutation(CREATE_EMPTY_COURSE);
    const [updateCourse, { loading: updating, error: updateError }] =
        useMutation(UPDATE_COURSE_DETAILS, {
            refetchQueries: [GET_COURSE],
        });
    const [publishCourse, { loading: publishing, error: publishError }] =
        useMutation(PUBLISH_COURSE, {
            refetchQueries: [GET_COURSE],
        });
    const [unpublishCourse, { loading: unpublishing, error: unpublishError }] =
        useMutation(UNPUBLISH_COURSE, {
            refetchQueries: [GET_COURSE],
        });
    const [deleteCourse, { loading: deleting, error: deleteError }] =
        useMutation(DELETE_COURSE);
    const [
        createChapter,
        { loading: creatingChapter, error: createChapterError },
    ] = useMutation(CREATE_CHAPTER, {
        refetchQueries: [GET_COURSE],
    });
    const [
        updateChapter,
        { loading: updatingChapter, error: updateChapterError },
    ] = useMutation(UPDATE_CHAPTER, {
        refetchQueries: [GET_COURSE],
    });
    const [
        deleteChapter,
        { loading: deletingChapter, error: deleteChapterError },
    ] = useMutation(DELETE_CHAPTER, { refetchQueries: [GET_COURSE] });
    const [
        createSection,
        { loading: creatingSection, error: createSectionError },
    ] = useMutation(CREATE_SECTION, {
        refetchQueries: [GET_COURSE],
    });
    const [
        deleteSection,
        { loading: deletingSection, error: deleteSectionError },
    ] = useMutation(DELETE_SECTION, { refetchQueries: [GET_COURSE] });
    const currentCourse = useMemo(
        () =>
            currentCourseData?.getCourseById || {
                requirements: [],
                objectives: [],
                targets: [],
                resources: [],
                chapters: [],
            },
        [currentCourseData?.getCourseById],
    );
    const confirm = useConfirm();

    const previousCreating = useRef<boolean>(false);
    // const { chapter, section } = useMemo(() => {
    //     const match = matchPath<{
    //         courseId: string;
    //         chapterId: string;
    //         sectionId: string;
    //     }>(location.pathname, {
    //         path: "/courses/:courseId/chapters/:chapterId/sections/:sectionId/edit",
    //         exact: true,
    //         strict: false,
    //     });
    //     if (match) {
    //         const { courseId, chapterId, sectionId } = match.params;

    //         return { chapter: selectedChapter, section: selectedSection };
    //     }
    //     return { chapter: null, section: null };
    // }, [current?.chapters, location.pathname]);

    useEffect(() => {
        if (params.id === "new" && !previousCreating.current && !creating) {
            createEmptyCourse();
        }

        if (params.id !== "new" && currentCourse?.id !== params.id) {
            getCourse({ variables: { courseId: params.id } });
        }

        if (previousCreating.current && !creating) {
            history.push(`/courses/${newCourse?.createCourse?.id}/details`);
        }

        previousCreating.current = creating;
    }, [
        createEmptyCourse,
        creating,
        currentCourse,
        currentCourse?.id,
        getCourse,
        history,
        params.id,
        newCourse?.createCourse?.id,
    ]);

    const handleOpenCourses = useCallback(() => {
        history.push("/courses");
    }, [history]);

    const handleOpenCourse = useCallback(() => {
        history.push(`/courses/${currentCourse?.id}/curriculum`);
    }, [currentCourse?.id, history]);

    const handlePublishCourse = useCallback(() => {
        (currentCourse?.status === "public" ? unpublishCourse : publishCourse)({
            variables: { courseId: currentCourse?.id },
        });
    }, [currentCourse, publishCourse, unpublishCourse]);

    const handleDeleteCourse = useCallback(() => {
        confirm({
            title: "Are you sure you want to delete the course?",
            description: `You cannot restore it once you delete. This action will permanently delete "${currentCourse?.title || "<anonymous chapter>"}", including its chapters.`,
            confirmationText: "Yes, I understand. Delete anyway.",
            confirmationButtonProps: {
                color: "secondary",
                variant: "contained",
            },
        }).then(() => {
            deleteCourse({
                variables: { courseId: currentCourse.id },
            });
            history.push("/courses");
        });
    }, [confirm, currentCourse.id, currentCourse?.title, deleteCourse, history]);

    const handleUpdateCourse = useCallback(
        (updates: any) => {
            updateCourse({
                variables: updates,
            });
        },
        [updateCourse],
    );

    const handleCreateChapter = useCallback(
        (chapter: any) => {
            createChapter({
                variables: chapter,
            });
        },
        [createChapter],
    );

    const handleUpdateChapter = useCallback(
        (chapter: any) => {
            updateChapter({
                variables: chapter,
            });
        },
        [updateChapter],
    );

    const handleDeleteChapter = useCallback(
        (chapterId: string) => {
            deleteChapter({
                variables: {
                    chapterId,
                },
            });
        },
        [deleteChapter],
    );

    const handleCreateSection = useCallback(
        (section: any) => {
            createSection({
                variables: section,
            });
        },
        [createSection],
    );

    const handleDeleteSection = useCallback(
        (chapterId: string, sectionId: string) => {
            deleteSection({
                variables: {
                    sectionId,
                },
            });
        },
        [deleteSection],
    );

    // TODO: prevent reordering while saving!

    if (params.id === "new" || creating || !currentCourse || fetching) {
        return (
            <div className={classes.suspense}>
                <CircularProgress size="48px" />
            </div>
        );
    }

    return (
        <div className={classes.root}>
            <div className={classes.top}>
                <AppBar position="sticky">
                    <Toolbar className={classes.toolbar}>
                        <div className={classes.toolbarContent}>
                            <Breadcrumbs separator="">
                                <Link
                                    color="inherit"
                                    onClick={handleOpenCourses}
                                    className={classes.link}>
                                    Courses
                                </Link>

                                {!currentCourse?.title && (
                                    <Typography color="textPrimary">
                                        New Course
                                    </Typography>
                                )}

                                {currentCourse?.title && (
                                    <Link
                                        color="inherit"
                                        onClick={handleOpenCourse}
                                        className={classes.link}>
                                        {currentCourse?.title}
                                    </Link>
                                )}
                                {/* 
                                {chapter && (
                                    <Typography color="textPrimary">
                                        {chapter?.title ?? "<impossible>"}
                                    </Typography>
                                )}
                                {section && (
                                    <Typography color="textPrimary">
                                        {section?.title ?? "<impossible>"}
                                    </Typography>
                                )} */}
                            </Breadcrumbs>
                            <div className={classes.toolbarActions}>
                                <Button
                                    color="inherit"
                                    onClick={handlePublishCourse}
                                    className={classes.action}>
                                    <Icon
                                        className={classes.actionIcon}
                                        fontSize="small">
                                        publish
                                    </Icon>
                                    {currentCourse?.status === "public"
                                        ? "Unpublish"
                                        : "Publish"}
                                </Button>
                                <Button
                                    color="inherit"
                                    onClick={handleDeleteCourse}
                                    className={classes.action}>
                                    <Icon
                                        className={classes.actionIcon}
                                        fontSize="small">
                                        delete
                                    </Icon>
                                    Delete
                                </Button>
                            </div>
                        </div>
                    </Toolbar>
                </AppBar>
            </div>
            <div className={classes.bottom}>
                <div className={classes.navigation}>
                    <CourseNavigation id={currentCourse.id} />
                </div>
                <div className={classes.container}>
                    <Switch>
                        <Route exact={true} path="/courses/:id/details">
                            <CourseDetails
                                {...currentCourse}
                                updating={false}
                                onUpdate={handleUpdateCourse}
                            />
                        </Route>
                        <Route exact={true} path="/courses/:id/audience">
                            <CourseAudience
                                {...currentCourse}
                                updating={false}
                                onUpdate={handleUpdateCourse}
                            />
                        </Route>
                        <Route exact={true} path="/courses/:id/pricing">
                            <CoursePricing
                                {...currentCourse}
                                updating={false}
                                onUpdate={handleUpdateCourse}
                            />
                        </Route>
                        <Route exact={true} path="/courses/:id/curriculum">
                            <CourseCurriculum
                                {...currentCourse}
                                courseId={currentCourse.id}
                                onUpdateChapter={handleUpdateChapter}
                                onCreateChapter={handleCreateChapter}
                                onDeleteChapter={handleDeleteChapter}
                                onCreateSection={handleCreateSection}
                                onDeleteSection={handleDeleteSection}
                            />
                        </Route>
                        <Route exact={true} path="/courses/:id/promotions">
                            <CoursePromotions />
                        </Route>
                        <Route
                            exact={true}
                            path="/courses/:courseId/chapters/:chapterId/sections/:sectionId/edit">
                            <ContentEditor />
                        </Route>
                        <Redirect to={`/courses/${params.id}/details`} />
                    </Switch>
                </div>
            </div>
        </div>
    );
};

export default CourseEditor;
