import { FunctionComponent, useState, useCallback, useEffect } from "react";
import {
    Card,
    CardContent,
    CardActions,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    Icon,
    Button,
    CircularProgress,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Formsy from "formsy-react";
import { by639_1 } from "iso-language-codes";

import TextField from "../common/FormsyTextField";

const identifiers: string[] = Object.keys(by639_1);

const levels = [
    {
        title: "Beginner",
        value: "beginner",
    },
    {
        title: "Intermediate",
        value: "intermediate",
    },
    {
        title: "Expert",
        value: "expert",
    },
    {
        title: "All Levels",
        value: "all_levels",
    },
];

const useStyles = makeStyles((theme) => ({
    card: {
        width: 700,
    },
    content: {
        padding: theme.spacing(2),
    },
    form: {
        display: "flex",
        flexDirection: "column",
    },
    title: {},
    subtitle: {
        marginTop: theme.spacing(3),
    },
    description: {
        marginTop: theme.spacing(3),
    },
    level: {
        marginTop: theme.spacing(3),
    },
    languageCode: {
        marginTop: theme.spacing(3),
    },
    actions: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-end",
        width: "100%",
        padding: theme.spacing(2),
    },
    action: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2),
        width: 128,
    },
    actionIcon: {
        marginRight: theme.spacing(1),
    },
    progress: {
        marginRight: theme.spacing(1),
    },
}));

interface Props {
    id: string;
    title: string;
    description: string;
    level: string;
    languageCode: string;
    updating: boolean;
    onUpdate: (course: any) => void;
}

const CourseDetails: FunctionComponent<Props> = (props: Props) => {
    const { updating, onUpdate } = props;
    const classes = useStyles();
    const [title, setTitle] = useState("");
    const [description, setDescription] = useState("");
    const [level, setLevel] = useState("beginner");
    const [languageCode, setLanguageCode] = useState("en");
    const [valid, setValid] = useState(false);

    const changesMade =
        props.title !== title ||
        props.description !== description ||
        props.level !== level ||
        props.languageCode !== languageCode;

    /* Propogate changes in props to our local state. */
    useEffect(() => {
        setTitle(props.title);
        setDescription(props.description);
        setLevel(props.level);
        setLanguageCode(props.languageCode);
    }, [props.description, props.languageCode, props.level, props.title]);

    const handleTitleChange = useCallback((event) => {
        setTitle(event.target.value);
    }, []);

    const handleDescriptionChange = useCallback((event) => {
        setDescription(event.target.value);
    }, []);
    const handleLevelChange = useCallback((event) => {
        setLevel(event.target.value);
    }, []);

    const handleLanguageCodeChange = useCallback((event) => {
        setLanguageCode(event.target.value);
    }, []);

    const handleValidForm = useCallback(() => {
        setValid(true);
    }, []);

    const handleInvalidForm = useCallback(() => {
        setValid(false);
    }, []);

    const handleSubmit = useCallback(() => {
        onUpdate({
            courseId: props.id,
            title,
            description,
            level,
            languageCode,
        });
    }, [description, languageCode, level, onUpdate, props.id, title]);

    return (
        <Card className={classes.card}>
            <CardContent className={classes.content}>
                <Formsy
                    className={classes.form}
                    onValid={handleValidForm}
                    onInvalid={handleInvalidForm}>
                    <TextField
                        name="title"
                        className={classes.title}
                        label="Title"
                        validations={{
                            maxLength: 504,
                        }}
                        validationErrors={{
                            maxLength:
                                "The title should not be longer than 504 characters",
                        }}
                        variant="outlined"
                        size="medium"
                        value={title}
                        onChange={handleTitleChange}
                        required={true}
                    />
                    <TextField
                        name="description"
                        className={classes.description}
                        label="Description"
                        validations={{
                            maxLength: 1020,
                        }}
                        validationErrors={{
                            maxLength:
                                "The description should not be longer than 1024 characters",
                        }}
                        variant="outlined"
                        size="medium"
                        multiline={true}
                        rows={10}
                        value={description}
                        onChange={handleDescriptionChange}
                    />
                    <FormControl
                        variant="outlined"
                        className={classes.level}
                        size="medium">
                        <InputLabel id="levelLabel">Level</InputLabel>
                        <Select
                            labelId="levelLabel"
                            value={level}
                            onChange={handleLevelChange}
                            label="Level">
                            {levels.map((level) => (
                                <MenuItem key={level.title} value={level.value}>
                                    {level.title}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    <FormControl
                        variant="outlined"
                        className={classes.languageCode}
                        size="medium">
                        <InputLabel id="languageCodeLabel">Language</InputLabel>
                        <Select
                            labelId="languageCodeLabel"
                            value={languageCode}
                            onChange={handleLanguageCodeChange}
                            label="Language">
                            {identifiers.map((identifier: string) => {
                                const language: {
                                    name: string;
                                } = by639_1[identifier];
                                return (
                                    <MenuItem
                                        key={identifier}
                                        value={identifier}>
                                        {language.name}
                                    </MenuItem>
                                );
                            })}
                        </Select>
                    </FormControl>
                </Formsy>
            </CardContent>
            <CardActions className={classes.actions}>
                <Button
                    className={classes.action}
                    variant="contained"
                    color="primary"
                    disabled={!changesMade || !valid || updating}
                    onClick={handleSubmit}>
                    {!updating && (
                        <Icon className={classes.actionIcon} fontSize="small">
                            save
                        </Icon>
                    )}
                    {updating && (
                        <CircularProgress
                            size="16px"
                            color="inherit"
                            className={classes.progress}
                        />
                    )}
                    Save
                </Button>
            </CardActions>
        </Card>
    );
};

export default CourseDetails;
