import { useState, useCallback, FunctionComponent } from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
    Card,
    CardContent,
    CardActions,
    FormControl,
    IconButton,
    Icon,
    InputLabel,
    Select,
    MenuItem,
} from "@material-ui/core";
import Formsy from "formsy-react";
import lodash from "lodash";

import TextField from "../common/FormsyTextField";
import { QuestionType, QuestionOption } from "../../types";
import QuestionOptionUI from "./QuestionOption";

const useStyles = makeStyles((theme) => ({
    card: {
        marginTop: theme.spacing(4),
        maxWidth: 800,
    },
    content: {},
    question: {},
    type: {
        marginTop: theme.spacing(2),
        maxWidth: 400,
    },
    options: {
        marginTop: theme.spacing(3),
    },
    actions: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-end",
    },
}));

interface Props {
    index: number;
    text: string;
    type: "single_correct_option" | "multiple_correct_options";
    options: QuestionOption[];
    onTextChange: (index: number, text: string) => void;
    onTypeChange: (
        index: number,
        type: "single_correct_option" | "multiple_correct_options",
    ) => void;
    onOptionsChange: (index: number, options: QuestionOption[]) => void;
    onNew: (index: number) => void;
}

const EMPTY_OPTION: QuestionOption = {
    text: "",
    correct: false,
};

const QuestionEditor: FunctionComponent<Props> = (props: Props) => {
    const {
        index,
        text,
        type,
        options,
        onTextChange,
        onTypeChange,
        onNew,
        onDuplicate,
        onDelete,
        onOptionsChange,
    } = props;
    const classes = useStyles();
    const [valid, setValid] = useState(false);

    const handleValidForm = useCallback(() => {
        setValid(true);
    }, []);

    const handleInvalidForm = useCallback(() => {
        setValid(false);
    }, []);

    const handleTextChange = useCallback(
        (event) => {
            onTextChange(index, event.target.value);
        },
        [onTextChange, index],
    );

    const handleTypeChange = useCallback(
        (event) => {
            const newOptions = lodash.cloneDeep(options);
            for (const option of newOptions) {
                option.correct = false;
            }
            onTypeChange(index, event.target.value as QuestionType);
        },
        [options, index, onTypeChange],
    );

    const handleNewQuestion = useCallback(() => {
        onNew(index);
    }, [onNew, index]);

    const handleDuplicateQuestion = useCallback(() => {
        onDuplicate(index);
    }, [index, onDuplicate]);

    const handleDeleteQuestion = useCallback(() => {
        onDelete(index);
    }, [index, onDelete]);

    const handleOptionTextChange = useCallback(
        (optionIndex: number, text: string) => {
            const newOption = {
                ...options[optionIndex],
                text,
            };
            const newOptions = [...options];
            newOptions[optionIndex] = newOption;
            onOptionsChange(index, newOptions);
        },
        [index, onOptionsChange, options],
    );

    const handleOptionCorrectChange = useCallback(
        (optionIndex: number, correct: boolean) => {
            const newOptions = [...options];

            if (type === "single_correct_option") {
                const oldOptionIndex = options.findIndex(
                    (option) => option.correct,
                );
                if (oldOptionIndex >= 0) {
                    const oldOption = {
                        ...options[oldOptionIndex],
                        correct: false,
                    };
                    newOptions[oldOptionIndex] = oldOption;
                }
            }

            const newOption = {
                ...options[optionIndex],
                correct,
            };
            newOptions[optionIndex] = newOption;
            onOptionsChange(index, newOptions);
        },
        [index, onOptionsChange, options, type],
    );

    const handleNewOption = useCallback(
        (optionIndex: number) => {
            const newOptions: QuestionOption[] = [
                ...options.slice(0, optionIndex + 1),
                EMPTY_OPTION,
                ...options.slice(optionIndex + 1),
            ];
            onOptionsChange(index, newOptions);
        },
        [index, onOptionsChange, options],
    );

    const handleDeleteOption = useCallback(
        (optionIndex: number) => {
            const newOptions = [
                ...options.slice(0, optionIndex),
                ...options.slice(optionIndex + 1),
            ];
            onOptionsChange(index, newOptions);
        },
        [index, onOptionsChange, options],
    );

    const renderOption = (option: QuestionOption, index: number) => (
        <QuestionOptionUI
            key={index}
            index={index}
            type={type}
            text={option.text}
            correct={option.correct}
            onDelete={(options.length > 2 && handleDeleteOption) || undefined}
            onNew={handleNewOption}
            onTextChange={handleOptionTextChange}
            onCorrectChange={handleOptionCorrectChange}
        />
    );

    return (
        <Card className={classes.card}>
            <CardContent className={classes.content}>
                <Formsy onValid={handleValidForm} onInvalid={handleInvalidForm}>
                    <div className={classes.header}>
                        <TextField
                            name="question"
                            className={classes.question}
                            label="Question"
                            validations={{
                                minLength: 8,
                                maxLength: 504,
                            }}
                            validationErrors={{
                                minLength:
                                    "The question should have 8-504 characters",
                                maxLength:
                                    "The question should have 8-504 characters",
                            }}
                            variant="outlined"
                            size="medium"
                            value={text}
                            onChange={handleTextChange}
                            required={true}
                            fullWidth={true}
                            multiline={true}
                            minRows={1}
                        />

                        <FormControl
                            variant="outlined"
                            className={classes.type}
                            fullWidth={true}>
                            <InputLabel id="type">Type</InputLabel>
                            <Select
                                labelId="type"
                                value={type}
                                onChange={handleTypeChange}
                                label="Type">
                                <MenuItem value="single_correct_option">
                                    Single Correct Option
                                </MenuItem>
                                <MenuItem value="multiple_correct_options">
                                    Multiple Correct Options
                                </MenuItem>
                            </Select>
                        </FormControl>
                    </div>

                    <div className={classes.options}>
                        {options.map(renderOption)}
                    </div>
                </Formsy>
            </CardContent>
            <CardActions disableSpacing={true} className={classes.actions}>
                <IconButton onClick={handleNewQuestion}>
                    <Icon fontSize="small">add_circle</Icon>
                </IconButton>
                <IconButton onClick={handleDuplicateQuestion}>
                    <Icon fontSize="small">content_copy</Icon>
                </IconButton>
                <IconButton onClick={handleDeleteQuestion}>
                    <Icon fontSize="small">delete</Icon>
                </IconButton>
            </CardActions>
        </Card>
    );
};

export default QuestionEditor;
