import { Fragment, FunctionComponent, useCallback } from "react";
import {
    Button,
    List,
    ListItem,
    ListItemIcon,
    ListItemText,
    Divider,
    Icon,
} from "@material-ui/core";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import { useLocation, useHistory } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        backgroundColor: (theme.palette.background as any).paper,
        height: 312,
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-between",
        alignItems: "flex-end",
        borderRadius: theme.shape.borderRadius,
        marginRight: theme.spacing(2),
    },
    groups: {
        width: "100%",
    },
    group: {},
    listItemIcon: {
        minWidth: 40,
    },
    listItemText: {
        color: "white",
        fontSize: 16,
    },
}));

interface GroupItem {
    id: string;
    title: string;
    icon: string;
    url: string;
}

interface Group {
    id: string;
    children: GroupItem[];
}

const groups: Group[] = [
    {
        id: "general",
        children: [
            {
                id: "details",
                title: "Details",
                icon: "lightbulb",
                url: "/courses/:id/details",
            },
            {
                id: "audience",
                title: "Audience",
                icon: "person",
                url: "/courses/:id/audience",
            },
            {
                id: "pricing",
                title: "Pricing",
                icon: "monetization_on",
                url: "/courses/:id/pricing",
            },
            {
                id: "curriculum",
                title: "Curriculum",
                icon: "checklist",
                url: "/courses/:id/curriculum",
            },
            {
                id: "promotions",
                title: "Promotions",
                icon: "emoji_events",
                url: "/courses/:id/promotions",
            },
        ],
    },
];

export interface Props {
    id: string;
}

const CourseNavigation: FunctionComponent<Props> = (props: Props) => {
    const { id } = props;
    const classes = useStyles();
    const location = useLocation();
    const history = useHistory();
    const theme = useTheme();

    const handleClick = (id: string, url: string) => () => {
        // trackSideNavigationClick(id);

        if (url.startsWith("/")) {
            history.push(url);
        } else {
            window.open(url);
        }
    };

    const renderItem = (item: GroupItem) => {
        const selected = location.pathname === item.url;
        return (
            <ListItem
                key={item.id}
                button={true}
                onClick={handleClick(item.id, item.url.replace(":id", id))}>
                <ListItemIcon classes={{ root: classes.listItemIcon }}>
                    <Icon
                        fontSize="small"
                        color={selected ? "secondary" : undefined}>
                        {item.icon}
                    </Icon>
                </ListItemIcon>
                <ListItemText
                    primary={item.title}
                    classes={{ primary: classes.listItemText }}
                    style={{
                        color: selected
                            ? theme.palette.secondary.main
                            : undefined,
                    }}
                />
            </ListItem>
        );
    };

    const renderGroup = (group: Group, index: number) => (
        <Fragment key={group.id}>
            <List component="nav" className={classes.group}>
                {group.children.map(renderItem)}
            </List>
            {index + 1 < groups.length && <Divider />}
        </Fragment>
    );

    return (
        <div className={classes.root}>
            <div className={classes.groups}>{groups.map(renderGroup)}</div>
        </div>
    );
};

export default CourseNavigation;
