import { FunctionComponent, useState, useCallback } from "react";
import {
    Card,
    CardContent,
    Typography,
    Icon,
    IconButton,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { Draggable } from "react-beautiful-dnd";
import { useConfirm } from "material-ui-confirm";

import { SectionType } from "../../types";

const useStyles = makeStyles((theme) => ({
    content: {
        padding: theme.spacing(3),
    },
    chapter: {
        background: theme.palette.background.paper,
        userSelect: "none",
        marginTop: theme.spacing(2),
    },
    chapterHeader: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-start",
        alignItems: "center",
    },
    chapterNumberIcon: {
        color: "white",
        marginRight: theme.spacing(1),
    },
    chapterNumber: { color: "white", marginRight: theme.spacing(2) },
    chapterTitle: { color: "white" },
    header: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-start",
        alignItems: "center",
    },
    sectionNumberIcon: {
        marginRight: theme.spacing(1),
    },
    sectionNumber: {
        fontSize: 14,
        width: 88,
    },
    sectionTypeIcon: {
        marginRight: theme.spacing(1),
    },
    sectionTitle: {
        fontSize: 14,
        fontWeight: 500,
    },
    actions: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-end",
        alignItems: "center",
        marginLeft: "auto",
    },
    dragIndicator: {
        width: 20,
        height: 20,
    },
}));

const iconByType = {
    video: "smart_display",
    article: "article",
    quiz: "live_help",
    coding_excercise: "code",
    assignment: "assignment_turned_in",
};

export interface Props {
    chapterId: string;
    sectionId: string;
    title: string;
    index: number;
    type: SectionType;
    onDelete: (chapterId: string, sectionId: string) => void;
    onEdit: (chapterId: string, sectionId: string) => void;
}

const CourseSection: FunctionComponent<Props> = (props: Props) => {
    const { chapterId, sectionId, title, index, type, onDelete, onEdit } =
        props;
    const [hover, setHover] = useState(false);
    const classes = useStyles();
    const confirm = useConfirm();

    const onMouseEnter = useCallback(() => {
        setHover(true);
    }, []);

    const onMouseLeave = useCallback(() => {
        setHover(false);
    }, []);

    const handleDelete = useCallback(() => {
        confirm({
            title: "Are you sure you want to delete the section?",
            description: `You cannot restore it once you delete. This action will permanently delete "${title}", including its content.`,

            confirmationText: "Yes, I understand. Delete anyway.",
            confirmationButtonProps: {
                color: "secondary",
                variant: "contained",
            },
        }).then(() => {
            onDelete(chapterId, sectionId);
        });
    }, [confirm, title, onDelete, chapterId, sectionId]);

    const handleAddContent = useCallback(() => {
        onEdit(chapterId, sectionId);
    }, [chapterId, onEdit, sectionId]);

    return (
        <Draggable draggableId={sectionId} index={index}>
            {(provided, snapshot) => (
                <Card
                    elevation={0}
                    ref={provided.innerRef}
                    {...provided.draggableProps}
                    {...provided.dragHandleProps}
                    className={classes.chapter}
                    style={{
                        ...provided.draggableProps.style,
                        opacity: snapshot.isDragging ? 0.7 : 1,
                    }}
                    onMouseEnter={onMouseEnter}
                    onMouseLeave={onMouseLeave}>
                    <CardContent className={classes.content}>
                        <div className={classes.header}>
                            <Icon
                                fontSize="small"
                                className={classes.sectionNumberIcon}>
                                check_circle
                            </Icon>
                            <Typography className={classes.sectionNumber}>
                                Section {index + 1}
                            </Typography>
                            <Icon
                                fontSize="small"
                                className={classes.sectionTypeIcon}>
                                {iconByType[type]}
                            </Icon>
                            <Typography className={classes.sectionTitle}>
                                {title}
                            </Typography>

                            <div
                                className={classes.actions}
                                style={{
                                    visibility: hover ? "visible" : "hidden",
                                }}>
                                <IconButton
                                    size="small"
                                    onClick={handleAddContent}>
                                    <Icon fontSize="small">edit</Icon>
                                </IconButton>
                                <IconButton size="small" onClick={handleDelete}>
                                    <Icon fontSize="small">delete</Icon>
                                </IconButton>
                                <Icon
                                    fontSize="small"
                                    className={classes.dragIndicator}>
                                    drag_indicator
                                </Icon>
                            </div>
                        </div>
                    </CardContent>
                </Card>
            )}
        </Draggable>
    );
};

export default CourseSection;
