import { useState, useCallback, useEffect } from "react";
import { useHistory, useLocation } from "react-router-dom";
import useQuery from "./useQuery";

const DEFAULT_ROWS_PER_PAGE = 20;

export interface PaginationQuery {
    page: string;
    limit: string;
    startDate: string | Date;
    endDate: string | Date;
}

export interface Pagination {
    generateURL: (page: number, limit: number) => void;
    query: PaginationQuery;
    page: number;
    setPage: (page: number) => void;
    rowsPerPage: number;
    setRowsPerPage: (rowsPerPage: number) => void;
}

const usePagination = (): Pagination => {
    const query = useQuery() as PaginationQuery;
    const history = useHistory();
    const location = useLocation();
    const [page, setPage] = useState(parseInt(query.page, 10) || 0);
    const [rowsPerPage, setRowsPerPage] = useState(
        parseInt(query.limit, 10) || DEFAULT_ROWS_PER_PAGE,
    );
    console.log(location);

    const generateURL = useCallback(
        (page0: number, limit: number): void => {
            const params0 = new URLSearchParams(location.search);

            params0.append("page", page0.toString());
            params0.append("limit", limit.toString());

            history.push(`${location.pathname}?${params0.toString()}`);
        },
        [history, location.pathname, location.search],
    );

    useEffect(() => {
        if (query.page === undefined || query.limit === undefined) {
            generateURL(page, rowsPerPage);
        }
    }, [generateURL, page, query.page, query.limit, rowsPerPage]);

    if ("startDate" in query) {
        query.startDate = new Date(Number(query.startDate));
    }

    if ("endDate" in query) {
        query.endDate = new Date(Number(query.endDate));
    }

    return { generateURL, query, page, setPage, rowsPerPage, setRowsPerPage };
};

export default usePagination;
